import request from '../HttpRequest';
import defaultReject from '../HttpRequest/default-reject';
import keyBy from 'lodash/fp/keyBy';
import omit from 'lodash/fp/omit';

const keyById = keyBy('id');
const omitTime = omit(['startTime', 'endTime']);
const setUniqueId = x => {
  return { ...x, uniqueId: `meeting_${x.id}` };
};
const setCategoryId = x => {
  return { ...x, categoryId: x.meetingCategory.id };
};

function fixStartTimeToStartDate(x) {
  const { startTime, endTime } = x;
  return { ...omitTime(x), startDate: startTime, endDate: endTime };
}

function MeetingClass() {
  let loadedMeetings;

  function getMeetings() {
    if (!loadedMeetings) {
      return loadMeetings();
    }

    return getCachedMeetings();
  }

  function loadMeetings() {
    return request
      .get('/meetings')
      .then(x =>
        x.map(fixStartTimeToStartDate).map(setUniqueId).map(setCategoryId)
      )
      .then(keyById)
      .then(setCachedMeetings)
      .catch(defaultReject);
  }

  function setCachedMeetings(data) {
    loadedMeetings = data;
    return getCachedMeetings();
  }

  function getCachedMeetings() {
    return Promise.resolve(loadedMeetings);
  }

  function getCategories() {
    return Promise.resolve({
      1: {
        id: 1,
        name: 'MØTER',
        orderIndex: 1
      },
      2: {
        id: 2,
        name: 'Pressekonferanser',
        orderIndex: 1
      },
      3: {
        id: 3,
        name: 'Styremøte',
        orderIndex: 1
      },
      4: {
        id: 4,
        name: 'AU-møte',
        orderIndex: 1
      },
      5: {
        id: 5,
        name: 'Fellesmote',
        orderIndex: 1
      },
      6: {
        id: 6,
        name: 'Forbundsting NCF',
        orderIndex: 1
      },
      7: {
        id: 7,
        name: 'Nordisk kongress',
        orderIndex: 1
      },
      8: {
        id: 8,
        name: 'UCE kongress',
        orderIndex: 1
      },
      9: {
        id: 9,
        name: 'UCI kongress',
        orderIndex: 1
      }
    });
  }

  function getMeetingById(id) {
    return request
      .get('/meetings/' + id)
      .then(setUniqueId)
      .then(setCategoryId)
      .then(fixStartTimeToStartDate)
      .catch(defaultReject);
  }

  this.getAll = getMeetings;
  this.getCategories = getCategories;
  this.getById = getMeetingById;
}

export default MeetingClass;



