<script>
  import { runAtStart, runAtEnd } from '../helpers/debounce-extended';
  import EventListenersHelperClass from '../helpers/event-listener-helper';

  const eventListeners = new EventListenersHelperClass();

  export default {
    inserted(el) {
      let isHoverDisabled = false;
      const elClass = 'disable-hover';
      const regexClass = new RegExp('(\\s|^)' + elClass + '(\\s|$)');

      const disableHoverOn = runAtStart(400)(function disableHoverOn() {
        if (isHoverDisabled) {
          return;
        }
        isHoverDisabled = true;
        if (!el.className.match(regexClass)) {
          el.className += ' ' + elClass;
        }
      });

      const disableHoverOff = runAtEnd(400)(function() {
        if (!isHoverDisabled) {
          return;
        }
        isHoverDisabled = false;
        el.className = el.className.replace(regexClass, '');
      });
      eventListeners.addListener(el, 'scroll', disableHoverOn);
      eventListeners.addListener(el, 'scroll', disableHoverOff);
      eventListeners.init();
    },
    unbind() {
      eventListeners.destroy();
    }
  };
</script>



