<template>
  <h2 class="calendar_dropdown">
    <span class="dropdown_title"><span class="dropdown_link">{{monthTitle}} {{year}}</span></span>
    <select name="calendar-year" class="dropdown_select" v-model="currentDate" @change="onChange($event.target.value)">
      <option v-for="option in options" v-bind:value="option.getTime()">{{dateTitle(option)}}</option>
    </select>
  </h2>
</template>

<script>
  import dateFormatter from '../../helpers/date-helper';

  export default {
    props: {
      date: {
        type: Date,
        default() {
          return new Date();
        }
      }
    },
    data() {
      return {
        yearList: [2017],
        monthList: [
          'January',
          'February',
          'March',
          'April',
          'May',
          'June',
          'July',
          'August',
          'September',
          'October',
          'November',
          'December'
        ]
      };
    },
    computed: {
      options() {
        return this.yearList.reduce((result, year) => {
          this.monthList.forEach((month, indexMonth) => {
            const date = new Date(year, indexMonth, 1);
            result.push(date);
          });
          return result;
        }, []);
      },
      currentDate() {
        return new Date(
          this.date.getFullYear(),
          this.date.getMonth(),
          1
        ).getTime();
      },
      monthTitle() {
        return dateFormatter(this.date, 'MMMM'); // Month
      },
      year() {
        return this.date.getFullYear();
      }
    },
    methods: {
      dateTitle(date) {
        return dateFormatter(date, 'MMMM YYYY');
      },
      onChange(value) {
        this.$emit('change', Number(value));
      }
    }
  };
</script>

<style>
  @import "variables.css";

  .calendar_dropdown {
    position: relative;
    font-size: 1.7rem;
    margin-bottom: 0;
    margin-top: .5rem;
  }

  .calendar_dropdown span {
    font-weight: normal;
  }

  .dropdown_title {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    text-align: center;
    pointer-events: none;
  }

  .dropdown_link {
    border-bottom: 1px dashed var(--blueColor);
    color: var(--blueColor);
  }

  .dropdown_select {
    background: none;
    border: none;
    padding: 0;
    margin: 0;
    line-height: 1;
    height: auto;
    opacity: 0;
  }
</style>



