<template>
  <section class="calendar-wrapper">
    <div class="calendar">
      <calendar-navigation v-bind:date="routeDateObj" v-on:navigate="navigateByRoute"></calendar-navigation>
      <calendar-table v-bind:days="calendarDays" v-on:navigate="navigateByRoute"></calendar-table>
      <calendar-footer v-on:navigate="navigateByRoute"></calendar-footer>
    </div>
  </section>
</template>

<script>
  import router from '../../router';
  import reduce from 'lodash/fp/reduce';
  import dateFormatter from '../../helpers/date-helper';
  import { createDate } from '../../helpers/week-number';
  import generateCalendarDaysObject
    from '../../helpers/generate-calendar-month-array.js';

  import CalendarNavigation from './CalendarNavigation';
  import CalendarFooter from './CalendarFooter';
  import CalendarTable from './CalendarTable';

  const reduceTimelineOfEvents = (result, timestampKey) => {
    const key = dateFormatter(new Date(parseInt(timestampKey, 10)), 'YYYY-MM-DD');
    result[key] = (result[key] || 0) + 1;
    return result;
  };

  export default {
    components: {
      CalendarTable,
      CalendarNavigation,
      CalendarFooter
    },
    computed: {
      eventStartDates() {
        return this.$store.getters.startDatesOfFilteredActivities;
      },
      timelineOfEvents() {
        return reduce(reduceTimelineOfEvents, {})(this.eventStartDates);
      },
      currentDateObj() {
        return createDate(this.$store.state.currentDate);
      },
      routeDateObj() {
        return this.$store.getters.routeDate;
      },
      calendarDays() {
        const currentDateKey = dateFormatter(this.currentDateObj, 'YYYY-MM-DD');
        const routerDateKey = this.$store.state.route.params.date;

        const calendarDateObj = generateCalendarDaysObject(this.routeDateObj);

        return calendarDateObj.map(x => {
          const dateKey = dateFormatter(x.date, 'YYYY-MM-DD');
          return {
            ...x,
            currentClass: dateKey === currentDateKey,
            activeClass: dateKey === routerDateKey,
            events: this.timelineOfEvents[x.dateKey] || false
          };
        });
      }
    },
    methods: {
      navigateByRoute(date) {
        if (!date) {
          return;
        }
        return router.push({
          name: 'date',
          params: { ...{ date: dateFormatter(createDate(date), 'YYYY-MM-DD') } }
        });
      }
    }
  };
</script>

<style>
  @import "variables.css";

  .calendar-wrapper {
    text-align: center;
    border: none;
    margin: 0;
    overflow: hidden;
  }

  .calendar {
    text-align: center;
    overflow: hidden;
    padding: 0;
    background: #fff;
  }

  @media (--timeline-small) {
    .calendar-wrapper {
      margin: 2rem auto 0;
    }

    .calendar {
      border: 0;
      width: 100%;
      padding: 0 0 .5rem;
    }
  }
</style>



