<template>
  <form class="filter-list" action="">
    {{ t('Nivå').toUpperCase() }}
    <div class="filter-for-events" v-show="$store.state.showEventFilter">
      <label class="title">{{ t('Filters for events').toUpperCase() }}:</label>
      <event-filter-item
        :list="filterLevels"
        :callback="dispatchFilterBy('switchFilterByLevel')"
        :value="filterLevelValue"
        :type="filterStatus = true">{{ t('Nivå').toUpperCase() }}
      </event-filter-item>
      <event-filter-item
        :list="filterRegions"
        :callback="dispatchFilterBy('switchFilterByRegion')"
        :value="filterRegionValue"
        :type="filterStatus = true">{{ t('Regioner').toUpperCase() }}
      </event-filter-item>
      <event-filter-item
        v-for="category in filterCategories"
        :key="category.id"
        :list="category.options"
        :callback="dispatchFilterBy('switchFilterByCategory')"
        :value="filterCategoryValue"
        :type="filterStatus = true">{{category.name.toUpperCase()}}
      </event-filter-item>
      <event-filter-item
        :list="filterOrganizations"
        :callback="dispatchFilterBy('switchFilterByOrganization')"
        :value="filterOrganizationValue"
        :type="filterStatus = true">{{ t('Organization').toUpperCase() }}
      </event-filter-item>
    </div>
    <div class="filter-for-meetings" v-show="$store.state.showMeetingsFilter">
      <label class="title">{{ t('For Meetings').toUpperCase() }}:</label>
      <event-filter-item
        :list="filterMeetings"
        :callback="dispatchFilterBy('switchFilterByMeeting')"
        :value="filterMeetingsValue"
        :type="filterStatus = false">{{ t('Meetings').toUpperCase() }}
      </event-filter-item>
    </div>
    <button v-if="isAnyFilterActive" v-translate @click="resetFilters">Reset all filters</button>
  </form>
</template>

<script>
  import map from 'lodash/fp/map';
  import pick from 'lodash/fp/pick';
  import EventFilterItem from './EventFilterItem';

  const pickIdAndName = pick(['id', 'name', 'orderIndex', 'deleted']);
  const getOptions = map(pickIdAndName);

  export default {
    data() {
      return {
        filterStatus: ''
      }
    },
    components: {
      EventFilterItem
    },
    computed: {
      filterOrganizations() {
        return getOptions(this.$store.state.organizations);
      },
      filterOrganizationValue() {
        return this.$store.state.eventsFilter.organizationId || '';
      },
      filterLevels() {
        return getOptions(this.$store.state.levels);
      },
      filterLevelValue() {
        return this.$store.state.eventsFilter.levelId || '';
      },
      filterRegions() {
        return getOptions(this.$store.state.regions);
      },
      filterRegionValue() {
        return this.$store.state.eventsFilter.regionId || '';
      },
      filterCategories() {
        return this.$store.getters.categoriesTree;
      },
      filterCategoryValue() {
        return this.$store.state.eventsFilter.categoryId || '';
      },
      filterMeetings() {
        return getOptions(this.$store.state.categoriesOfMeetings);
      },
      filterMeetingsValue() {
        return this.$store.state.meetingsFilter.categoryId || '';
      },
      isAnyFilterActive() {
        return !this.$store.state.showEventFilter || !this.$store.state.showMeetingsFilter;
//        return map(x => x)(
//          this.$store.state.eventsFilter
//        ).reduce((result, value) => {
//          if (!value) {
//            return result;
//          }
//          return result + value;
//        }, 0);
      }
    },
    methods: {
      dispatchFilterBy(action) {
        return function(data) {
          return this.$store.dispatch(action, data);
        };
      },
      resetFilters() {
        this.$store.dispatch('resetFilters');
      },
      hideFilterByType(status) {
        this.$store.dispatch('hideFilter', status);
      }
    },
    mounted() {
      this.$on('showFilter', this.hideFilterByType);
    }
  };
</script>

<style>
  @import "variables.css";

  .filter-list {
    margin: .3rem 0;
    font-size: 0;
    box-sizing: border-box;
    font-family: var(--baseHeaderFontFamily);
    float: none;
  }

  .filter-list button {
    padding: .3rem 0;
    height: auto;
    letter-spacing: 0.5px;
    font-size: 12px;
  }

  .filter-for-events {
    margin-bottom: 20px;
  }
</style>



