import {
  runPeriodically,
  runAtStart,
  runAtEnd
} from '../helpers/debounce-extended';
import EventListenersHelperClass from '../helpers/event-listener-helper';

const eventListeners = new EventListenersHelperClass();

const scrollEvents = {
  mounted() {
    eventListeners.addListener(
      this.$el,
      'scroll',
      runAtStart(400)(this.onScrollStart)
    );
    eventListeners.addListener(
      this.$el,
      'scroll',
      runAtEnd(400)(this.onScrollEnd)
    );
    eventListeners.addListener(
      this.$el,
      'scroll',
      runPeriodically(300)(this.onScroll)
    );
    eventListeners.init();
  },
  beforeDestroy() {
    eventListeners.destroy();
  },
  methods: {
    onScrollStart() {},
    onScrollEnd() {},
    onScroll() {}
  }
};

export default scrollEvents;



