<template>
  <div class="social-buttons">
    <i @click="alert" class="icon icon-facebook"></i>
    <i @click="alert" class="icon icon-linkedin"></i>
    <i @click="alert" class="icon icon-twitter"></i>
    <i @click="alert" class="icon icon-gplus"></i>
  </div>
</template>
<style>
  .social-buttons {
    font-size:0;
  }
  .social-buttons i {
    color: #fff;
    background: #16407d;
    font-size: 1.3rem;
    width: 2em;
    height: 2em;
    line-height: 2em;
    text-align: center;
    cursor: pointer;
    display: inline-block;
    border: 0;
    padding: 0;
  }
  .social-buttons i + i {
    margin-left:.3rem;
  }
  .social-buttons i:hover {
    background: #ed4044;
  }
</style>
<script>
  export default {
    methods: {
      alert() {
        alert('Sorry this part in development!');
      }
    }
  };
</script>



