import debounce from 'lodash/debounce';
import throttle from 'lodash/throttle';

export const runAtStart = delay => f =>
  debounce(f, delay, {
    leading: true,
    trailing: false
  });

export const runAtEnd = delay => f =>
  debounce(f, delay, {
    leading: false,
    trailing: true
  });

export const runPeriodically = delay => f =>
  throttle(f, delay, {
    leading: true,
    trailing: true
  });

export default { runPeriodically, runAtStart, runAtEnd };



