<template>
  <div>{{markerAddress}}</div>
</template>

<script>
  import getAddress from '../../helpers/HttpRequest/gmaps';

  export default {
    props: ['event'],
    data: function() {
      return {
        markerAddress: ''
      };
    },
    mounted() {
      getAddress(this.event.latitude, this.event.longitude).then(data => {
        this.markerAddress = data.status == 'OK'
          ? data.results[0].formatted_address
          : '';
      });
    }
  };
</script>



