<template>
  <div class="timeline-event-loader"></div>
</template>
<script>
  import { dateParser } from '../../helpers/date-helper';
  import isEqual from 'lodash/fp/isEqual';

  const TimeEventLoader = {
    name: 'time-event-loader',
    props: {
      direction: {
        type: String,
        required: true
      }
    },
    data() {
      return {
        observer: false
      };
    },
    mounted() {
      const handler = this.direction === 'left'
        ? this.loadEventsLeft
        : this.loadEventsRight;

      this.observer = new IntersectionObserver(
        entries => {
          if (!entries[0].isIntersecting) {
            return;
          }
          this.$emit('beforeLoad');
          this.$nextTick(handler);
        },
        {
          // root: this.$parent.$el,
          threshold: [this.direction === 'left' ? 1 : 0]
        }
      );
      // Start observing an element
      this.observer.observe(this.$el);
    },
    beforeDestroy() {
      this.observer.disconnect();
    },
    computed: {
      shouldHandle() {
        return !this.$store.state.eventsStatus[
          this.direction === 'left' ? 'loadingLeft' : 'loadingRight'
        ];
      }
    },
    methods: {
      loadEventsLeft() {
        if (!this.shouldHandle) {
          return;
        }
        let leftSideData = this.$store.getters.outsideEvents.left;

        if (!leftSideData.timestamp) {
          leftSideData = {
            timestamp: dateParser(
              this.$store.state.route.params.date,
              'YYYY-MM-DD'
            ).getTime(),
            ids: []
          };
        }

        if (
          !leftSideData.timestamp ||
          this.isOutsideLeft(leftSideData.timestamp)
        ) {
          return;
        }

        this.$store.dispatch('loadEventsLeft', {
          date: leftSideData.timestamp,
          except: leftSideData.ids
        });
      },
      loadEventsRight() {
        if (!this.shouldHandle) {
          return;
        }
        let rightSideData = this.$store.getters.outsideEvents.right;

        if (!rightSideData.timestamp) {
          rightSideData = {
            timestamp: dateParser(
              this.$store.state.route.params.date,
              'YYYY-MM-DD'
            ).getTime(),
            ids: []
          };
        }

        if (
          rightSideData.timestamp &&
          this.isOutsideRight(rightSideData.timestamp)
        ) {
          return;
        }

        this.$store.dispatch('loadEventsRight', {
          date: rightSideData.timestamp,
          except: rightSideData.ids
        });
      },
      isOutsideLeft(timestamp) {
        return (
          this.$store.state.eventsStatus.limitLeft > 0 &&
          this.$store.state.eventsStatus.limitLeft >= timestamp
        );
      },
      isOutsideRight(timestamp) {
        return (
          this.$store.state.eventsStatus.limitRight > 0 &&
          timestamp >= this.$store.state.eventsStatus.limitRight
        );
      }
    }
  };

  export default TimeEventLoader;
</script>



