<template>
  <router-link class="event-item" :class="{ 'event-item__hover': showOverlay }" :to="link"
               @mouseover.native="mouseOver" @mouseleave.native="mouseLeave">
    <div class="event-box" :class="className">
      <div class="event-box--img">
        <slot name="image"></slot>
      </div>
      <div class="event-box--type">
        <slot name="title">Event</slot>
      </div>
    </div>
    <slot></slot>
  </router-link>
</template>

<script>
  export default {
    data() {
      return {
        mouseTimeout: undefined,
        showOverlay: false
      };
    },
    props: ['link', 'className'],
    methods: {
      mouseOver() {
        if (this.mouseTimeout) {
          return;
        }

        this.mouseTimeout = setTimeout(() => {
          this.showOverlay = true;
        }, 200);
      },
      mouseLeave() {
        if (!this.mouseTimeout) {
          return;
        }
        this.showOverlay = false;
        clearTimeout(this.mouseTimeout);
        this.mouseTimeout = undefined;
      }
    }
  };
</script>

<style>
  @import "variables.css";

  .event-item {
    margin: 0 0 1rem 1.5rem;
    width: 16rem;
    text-decoration: none;
    position: relative;
  }

  .event-item__hover {
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, .5);
  }

  .event-box {
    display: table;
    table-layout: fixed;
    height: 4.2rem;
    width: 16rem;
    margin-bottom: .5rem;
    cursor: pointer;
  }

  .event-box.lowercase span {
    text-transform: none;
    font-weight: 500
  }

  .event-box.blue-red .event-box--img {
    background: #16407d;
    border: none;
  }

  .event-box.blue-red .event-box--type {
    background: #e73e42;
    color: #fff;
    border: none;
  }

  .event-box--img {
    display: table-cell;
    width: 4rem;
    height: 4rem;
    vertical-align: middle;
    background: #9b9b9b;
    z-index: 1;
    border: none;
  }

  .event-box--img img {
    width: 3.8rem;
    height: 3.8rem;
    margin: .1rem auto;
    padding: 0;
    display: block;
  }

  .event-box--type {
    display: table-cell;
    vertical-align: middle;
    text-transform: uppercase;
    color: #16407d;
    font-size: 1.1rem;
    font-weight: bold;
    border: 2px solid #9b9b9b;
    border-left: 0;
  }

  .event-box--type > h3 {
    font-size: 1em;
    line-height: 1;
    padding: 0 .7rem;
    display: block
  }

  .compact-mode .event-item {
    margin-bottom: 2rem;
    width: 6rem;
    display: inline-block;
    vertical-align: top;
  }

  .compact-mode .event-item .event-box {
    width: 6rem;
  }

  .compact-mode .event-box--type {
    display: none !important;
  }

  /* Vertical layout for timeline */
  @media (--timeline-large) {
    .event-item {
      flex: 0 1 auto;
      align-self: auto;
      order: 0;
    }
  }

  @media (--timeline-small) {
    .event-item {
      margin: 0 2rem 2rem 0rem;
      display: inline-block;
      vertical-align: top;
    }
  }

  @media (--timeline-small) {
    .compact-mode .event-item:last-child {
      margin-bottom: 4rem;
    }

    .event-item,
    .event-box {
      width: 14.5rem;
    }

    .event-item {
      margin: 0 1rem 2rem 0;
    }
  }
</style>



