<template>
  <main class="timeline" v-smooth-scroll
        v-bind:class="{ 'compact-mode': classCompactMode, 'timeline__loading': isLoadingEventsLeft || isLoadingEventsRight }"
        ref="viewport">
    <timeline-event-loader direction="left" v-on:beforeLoad="changeRouteDate"></timeline-event-loader>
    <component v-for="event in events"
               v-bind:key="event.uniqueId"
               v-bind:is="event.component"
               v-bind:event="event.data"
               v-bind:ref="event.component">
    </component>
    <timeline-event-loader direction="right" v-on:beforeLoad="changeRouteDate"></timeline-event-loader>
  </main>
</template>

<style>
  @import "variables.css";

  .timeline {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
    box-sizing: border-box;
    height: calc(100vh);
    overflow: hidden;
    overflow-x: scroll;
    margin: 0 0 0 20rem;
    padding: var(--timelineTopPadding) 0 0 0rem;
    background: linear-gradient(to bottom, #4a90e2 0%, #4a90e2 100%);
    background-size: 3px 3px;
    background-repeat: repeat-x;
    background-position: 0 3rem;
  }

  .timeline__loading {
    overflow-x: hidden;
    opacity: .5;
  }

  .timeline__loading * {
    pointer-events: none !important;
  }

  @media (--header-small) {
    .timeline {
      height: calc(100vh - var(--headerHeightSm));
    }

  }

  /* Vertical layout for timeline */
  @media (--timeline-small) {
    .timeline {
      display: block;
      padding: 2rem 0 2em 4.2rem;
      margin-left: 0;
      overflow-x: hidden;
      overflow-y: auto;
      background-repeat: repeat-y;
      background-position: 2rem 0;
      width: 100%;
    }

    .timeline {
      margin-left: 0;
      margin-top: calc(var(--headerHeightSm));
      padding-top: 1rem;
    }

  }
</style>

<script>
  import dateFormatter from '../../helpers/date-helper';
  import debounce from 'lodash/debounce';
  import throttle from 'lodash/throttle';
  import minBy from 'lodash/fp/minBy';
  import maxBy from 'lodash/fp/maxBy';
  import map from 'lodash/fp/map';

  import router from '../../router/index';
  import { createDate } from '../../helpers/week-number';
  import { reduceEvents } from '../../helpers/EventsHelper/create-timeline-array';
  import TimelineEvent from './TimelineEvent';
  import TimelineMeeting from './TimelineMeeting';
  import TimelinePeriod from './TimelinePeriod';
  import TimelineTimeLabel from './TimelineTimeLabel.vue';
  import TimelineEventLoader from './TimelineEventLoader';
  import SmoothScroll from '../../directives/SmoothScroll';
  import smoothScroll from '../../helpers/smooth-scroll';
  import scrollEvents from '../../mixins/scrollEvents';
  import isInViewport from '../../helpers/in-viewport';
  import isInViewportMixin from '../../mixins/in-viewport';

  const minByEventDate = minBy(x => x.event.startDate);
  const maxByEventDate = maxBy(x => x.event.startDate);

  function getSpeedByOffset(offset) {
    const distance = Math.abs(offset.top) > Math.abs(offset.left)
      ? offset.top
      : offset.left;
    let speed = Math.round(Math.abs(distance) / 25);
    if (speed <= 100) speed = 100;
    if (speed >= 1000) speed = 1000;
    return speed;
  }

  function getElementOffsetByViewport(component, viewport) {
    const direction = {
      left: 0,
      top: 0
    };

    if (!component) {
      return direction;
    }

    const viewportRect = viewport;
    const eventRect = component.getBoundingClientRect();

    direction.left = eventRect.left - viewportRect.left - 15;
    direction.top = eventRect.top - viewportRect.top - 15;

    return direction;
  }

  export default {
    components: {
      TimelineEvent,
      TimelineMeeting,
      TimelinePeriod,
      TimelineTimeLabel,
      TimelineEventLoader
    },
    mixins: [scrollEvents, isInViewportMixin],
    directives: {
      SmoothScroll
    },
    methods: {
      onScrollEnd() {
        this.changeRouteDate();
      },
      getTimeLabelByDate(date) {
        const dateTimestamp = createDate(date).getTime();

        return this.$children
          .filter(
            v =>
              v.$options._componentTag === 'timeline-time-label' &&
              v.event.startDate.getTime() === dateTimestamp
          )
          .map(x => x.$el)[0];
      },
      getFirstVisibleTimelineLabel() {
        const visibleTimelineHeaders = (this.$refs['timeline-time-label'] || [])
          .filter(v => this.isInViewport(v.$el, 0));
        return minByEventDate(visibleTimelineHeaders);
      },
      changeRouteDate() {
        if (
          this.isLoadingEventsLeft ||
          this.isLoadingEventsRight ||
          this.$route.name !== 'date' ||
          this.isInViewport(
            this.getTimeLabelByDate(this.$store.state.route.params.date),
            0
          )
        ) {
          return;
        }

        requestAnimationFrame(() => {
          const visibleTimelineLabel = this.getFirstVisibleTimelineLabel();
          if (!visibleTimelineLabel) {
            return;
          }

          const routeSlug = dateFormatter(
            visibleTimelineLabel.event.startDate,
            'YYYY-MM-DD'
          );

          if (routeSlug && this.$route.params.date !== routeSlug) {
            router.replace({ name: 'date', params: { date: routeSlug } });
          }
        });
      },
      goToPrevTimeLabel(date) {
        const dateObj = createDate(date);
        const timeLabels = this.$children.filter(
          v => v.$options._componentTag === 'timeline-time-label'
        );
        let timeLabel =
          maxByEventDate(timeLabels.filter(v => v.event.startDate <= dateObj)) ||
          timeLabels[0];

        this.scrollToTimeLabel(timeLabel.$el);
      },
      goToNextTimeLabel(date) {
        const dateObj = createDate(date);
        const timeLabels = this.$children.filter(
          v => v.$options._componentTag === 'timeline-time-label'
        );
        let timeLabel =
          minByEventDate(timeLabels.filter(v => v.event.startDate >= dateObj)) ||
          timeLabels[timeLabels.length - 1];

        this.scrollToTimeLabel(timeLabel.$el);
      },
      restoreScrollPositionOnDate(date) {
        const timeLabel = this.getTimeLabelByDate(date);

        if (!timeLabel) {
          return;
        }

        this.moveToTimeLabel(timeLabel);
      },
      smoothScrollViewport(offset, duration) {
        return smoothScroll(this.$el, offset, duration);
      },
      scrollToTimeLabel(element) {
        this.$nextTick(() => {
          const offset = getElementOffsetByViewport(element, this.viewport);
          this.smoothScrollViewport(offset, getSpeedByOffset(offset));
        });
      },
      moveToTimeLabel(element) {
        this.$nextTick(() => {
          const offset = getElementOffsetByViewport(element, this.viewport);
          this.smoothScrollViewport(offset, 0, () => {
            console.log('remove scrollToDate');
          });
        });
      }
    },
    computed: {
      classCompactMode() {
        return this.$store.state.displayModeCompact;
      },
      isLoadingEventsLeft() {
        return this.$store.state.eventsStatus.loadingLeft;
      },
      isLoadingEventsRight() {
        return this.$store.state.eventsStatus.loadingRight;
      },
      events() {
        return reduceEvents(
          this.$store.getters.combinedFilteredEventsAndMeetings,
          this.$store.state.timelineMode
        );
      }
    },
    watch: {
      events() {
        this.$nextTick(() => {
          this.restoreScrollPositionOnDate(this.$store.state.route.params.date);
        });
      },
      '$store.state.route.params.date'(date, oldDate) {
        if (this.$store.state.route.params.id || date === oldDate) {
          return;
        }
        const calledMethod = createDate(date) < createDate(oldDate)
          ? 'goToPrevTimeLabel'
          : 'goToNextTimeLabel';
        this[calledMethod](date);
      }
    }
  };
</script>



