function iOSDetector() {
  var iDevices = ['iPad', 'iPhone', 'iPod'];
  if (navigator.platform) {
    while (iDevices.length) {
      if (
        navigator.platform === iDevices.pop() &&
        window.navigator.standalone
      ) {
        return true;
      }
    }
  }
  return false;
}

export default iOSDetector;



