import request from '../HttpRequest';
import defaultReject from '../HttpRequest/default-reject';
import keyBy from 'lodash/fp/keyBy';

const keyById = keyBy('id');

function CategoryClass() {
  let loadedCategories;

  function getCategories() {
    if (!loadedCategories) {
      return loadCategories();
    }

    return getCachedCategories();
  }

  function loadCategories() {
    return request
      .get('/categories')
      .then(data => data.filter(x => x.parentId !== 1))
      .then(keyById)
      .then(setCachedCategories)
      .catch(defaultReject);
  }

  function setCachedCategories(data) {
    loadedCategories = data;
    return getCachedCategories();
  }

  function getCachedCategories() {
    return Promise.resolve(loadedCategories);
  }

  this.getAll = getCategories;
}

export default CategoryClass;



