import isEqual from 'lodash/fp/isEqual';
import EventListenersHelperClass from '../helpers/event-listener-helper';
import { runPeriodically, runAtEnd } from '../helpers/debounce-extended';
import {
  getBoundingData,
  getScrollData,
  isInViewport
} from '../helpers/in-viewport';

const eventListeners = new EventListenersHelperClass();

const eventUpdate = new Event('update', {
  cancelable: false
});

const eventResize = new Event('resize', {
  cancelable: false
});

function dispatchEventToElement(element) {
  'use strict';
  return runPeriodically(300)(event => {
    if (element) {
      element.dispatchEvent(event);
    }
  });
}

const isInViewportMixin = {
  data() {
    'use strict';
    return {
      viewport: {},
      dispatchEvent: function() {}
    };
  },
  mounted() {
    this.dispatchEvent = dispatchEventToElement(this.$el);

    const dispatchEventResize = runPeriodically(300)(() => {
      this.dispatchEvent(eventResize);
    });
    const detectScrollAtEnd = runAtEnd(500)(this._detectScroll);
    const detectViewportAtEnd = runAtEnd(500)(this._detectViewport);
    const detectBoundaries = runPeriodically(300)(this._detectBoundaries);

    eventListeners.addListener(window, 'resize', dispatchEventResize);

    eventListeners.addListener(this.$el, 'update', this._detectViewport);
    eventListeners.addListener(this.$el, 'update', this._detectScroll);
    eventListeners.addListener(this.$el, 'update', detectBoundaries);

    eventListeners.addListener(this.$el, 'resize', detectBoundaries);
    eventListeners.addListener(this.$el, 'resize', detectScrollAtEnd);
    eventListeners.addListener(this.$el, 'resize', detectViewportAtEnd);

    eventListeners.addListener(this.$el, 'scroll', this._detectScroll);
    eventListeners.addListener(this.$el, 'scroll', detectBoundaries);

    this._detectViewport();
    this._detectScroll();

    eventListeners.init();
  },
  beforeDestroy() {
    eventListeners.destroy();
  },
  methods: {
    isInViewport(el, cushion) {
      'use strict';
      if (!el) {
        return false;
      }

      return isInViewport(this.viewport, el, cushion);
    },
    _detectViewport() {
      'use strict';
      this.viewport = Object.assign(
        {},
        this.viewport,
        getBoundingData(this.$el)
      );
    },
    _detectScroll() {
      'use strict';
      this.viewport = Object.assign({}, this.viewport, getScrollData(this.$el));
    },
    _detectBoundaries() {
      'use strict';
      const topLeftSide = {
        top: this.viewport.top - this.viewport.scrollTop,
        bottom: this.viewport.top - this.viewport.scrollTop + 1,
        left: this.viewport.left - this.viewport.scrollLeft,
        right: this.viewport.left - this.viewport.scrollLeft + 1
      };
      const bottomRightSide = {
        top:
          this.viewport.top -
          this.viewport.scrollTop +
          this.viewport.scrollHeight -
          1,
        bottom:
          this.viewport.top -
          this.viewport.scrollTop +
          this.viewport.scrollHeight,
        left:
          this.viewport.left +
          this.viewport.scrollWidth -
          this.viewport.scrollLeft -
          1,
        right:
          this.viewport.left +
          this.viewport.scrollWidth -
          this.viewport.scrollLeft
      };
    }
  },
  watch: {
    '$store.state.events'(value, oldValue) {
      if (isEqual(value)(oldValue)) {
        return;
      }
      this.$nextTick(() => {
        this.dispatchEvent(eventUpdate);
      });
    }
  }
};

export default isInViewportMixin;



