import request from '../HttpRequest';
import defaultReject from '../HttpRequest/default-reject';
import config from '../../config';

import keyBy from 'lodash/fp/keyBy';
import reject from 'lodash/fp/reject';

const keyById = keyBy('id');
const rejectDeleted = reject('deleted');
const extractEvent = function(x) {
  return x.event;
};
const removeNullProperties = function(x) {
  return Object.keys(x).reduce((result, key) => {
    return { ...result, [key]: x[key] === 'null' ? null : x[key] };
  }, {});
};
const setOrmObjectsToIds = function(x) {
  return {
    ...x,
    levelId: x.level.id,
    categoryId: x.category.id,
    organizationId: x.organization.id,
    regionId: x.region.id,
    raceId: x.race.id
  };
};
const setUniqueId = x => {
  return { ...x, uniqueId: `event_${x.id}` };
};

function EventClass() {
  function getEvents() {
    return request
      .get('/events')
      .then(rejectDeleted)
      .then(keyById)
      .catch(defaultReject)
      .catch([]);
  }

  function getLaterThan(date, excludedIds = []) {
    return request
      .post('/events/start_date_search', {
        startDate: date,
        excludedIds,
        limit: config.events_load,
        before: false
      })
      .then(events => {
        return events
          .map(extractEvent)
          .map(setUniqueId)
          .map(setOrmObjectsToIds)
          .map(removeNullProperties);
      })
      .then(keyById)
      .catch(defaultReject);
  }

  function getEarlierThan(date, excludedIds = []) {
    return request
      .post('/events/start_date_search', {
        startDate: date,
        excludedIds,
        limit: config.events_load,
        before: true
      })
      .then(events => {
        return events
          .map(extractEvent)
          .map(setUniqueId)
          .map(setOrmObjectsToIds)
          .map(removeNullProperties);
      })
      .then(keyById)
      .catch(defaultReject);
  }

  function getEventById(id) {
    return request
      .get('/events/' + id)
      .then(setUniqueId)
      .then(setOrmObjectsToIds)
      .catch(defaultReject);
  }

  function getFlatEvents(data) {
    return request
      .get('/events/range?from=' + data.start + '&to=' + data.end)
      .catch(defaultReject);
  }

  this.getAll = getEvents;
  this.getEarlierThan = getEarlierThan;
  this.getLaterThan = getLaterThan;

  this.getById = getEventById;

  this.getFlatEvents = getFlatEvents;
}

export default EventClass;



