export default {
  template: `<a :href="calendarUrl(calendar) || $parent.calendarUrl(calendar)" :class="calendarClass" target="_blank"><slot></slot></a>`,

  computed: {
    calendarClass() {
      return ['vue-add-to-calendar', `${this.calendar}-calendar`];
    }
  },
  methods: {
    calendarUrl() {
      return false;
    }
  }
};



