<template>
    <main>
      <div class="calendar_titles clearfix">
        <b v-for="value in daysLabels">{{value}}</b>
      </div>
      <div class="calendar_days clearfix">
        <calendar-td v-for="(value, index) in days" v-bind:id="'cal_day' + (index + 1)" v-bind:key="value.dateKey" v-bind:event="value" v-on:navigate="navigate"></calendar-td>
      </div>
    </main>
</template>

<script>
  import CalendarTd from './CalendarTd';
  import dateFormatter from '../../helpers/date-helper';

  export default {
    props: ['days'],
    data() {
      return {
        daysLabels: [
          dateFormatter(new Date('2017-05-21'), 'ddd'),
          dateFormatter(new Date('2017-05-22'), 'ddd'),
          dateFormatter(new Date('2017-05-23'), 'ddd'),
          dateFormatter(new Date('2017-05-24'), 'ddd'),
          dateFormatter(new Date('2017-05-25'), 'ddd'),
          dateFormatter(new Date('2017-05-26'), 'ddd'),
          dateFormatter(new Date('2017-05-27'), 'ddd')
        ]
      };
    },
    components: {
      CalendarTd
    },
    methods: {
      navigate(date) {
        this.$emit('navigate', date);
      }
    }
  };
</script>

<style>
  @import "variables.css";

  .calendar_titles > b {
    display: inline-block;
    font-weight: normal;
    text-transform: uppercase;
    color: #5e6066;
    border: 1px solid transparent;
    text-align: center;
    font-size: 9px;
    width: 13%;
  }

  .calendar_days {
    color: #7c8a95;
  }

  .calendar_days > span {
    display: inline-block;
    width: 13%;
    color: #000;
    text-align: center;
  }

  @media (--timeline-small) {
    .calendar_titles > b {
      width: 13.5%;
      font-size: 11px;
    }

    .calendar_day > a,
    .calendar_day > span {
      font-size: 14px !important;
      padding: 1.2rem 0 !important;
    }
  }
</style>



