import curry from 'lodash/fp/curry';
import groupBy from 'lodash/fp/groupBy';
import sortBy from 'lodash/fp/sortBy';
import maxBy from 'lodash/fp/maxBy';
import minBy from 'lodash/fp/minBy';
import map from 'lodash/fp/map';
import reduce from 'lodash/reduce';
import { getStartOf, getEndOf } from '../week-number';

const pickStartDate = x => minBy('startDate')(x).startDate;
const pickEndDate = x => maxBy('endDate')(x).endDate;
const sortByStartDate = sortBy('startDate');

const createSection = curry((type, data) => {
  return {
    component: 'timeline-' + type,
    type,
    data: Object.assign({}, data)
  };
});

const createSeparator = createSection('separator');
const createEvent = createSection('event');
const createMeeting = createSection('meeting');
const createPeriod = createSection('period');
const createTimeLabel = createSection('time-label');

export const groupEventsByMode = function(timelineMode) {
  'use strict';
  const getPeriodDate = getStartOf(timelineMode);
  const getPeriodTimestamp = x => getPeriodDate(x).getTime();
  return function(events) {
    return groupBy(x => getPeriodTimestamp(x.startDate))(
      sortByStartDate(events)
    );
  };
};

export const decomposeGroupToTimeline = function(
  groupsOfEvents,
  fnGetStartOfPeriod,
  fnGetEndOfPeriod
) {
  'use strict';
  let previousDate = -Infinity;

  const events = reduce(
    groupsOfEvents,
    (result, group) => {
      const startOfPeriod = fnGetStartOfPeriod(pickStartDate(group));
      const endOfPeriod = fnGetEndOfPeriod(pickEndDate(group));

      result.push(
        createPeriod({ startDate: previousDate, endDate: startOfPeriod })
      );
      result.push(
        createTimeLabel({ startDate: startOfPeriod, endDate: endOfPeriod })
      );

      Array.prototype.push.apply(result, generateEvents(group));

      previousDate = endOfPeriod;
      return result;
    },
    []
  );

  events.push(createPeriod({ startDate: previousDate, endDate: +Infinity }));

  return events;
};

export const generateEvents = function(events) {
  'use strict';
  console.log();
  return events.map(
    x => (x.type === 'meeting' ? createMeeting(x) : createEvent(x))
  );
};

export const reduceEvents = function(events, timelineMode) {
  'use strict';
  const groupOfEventsByMode = groupEventsByMode(timelineMode)(events);
  return decomposeGroupToTimeline(
    groupOfEventsByMode,
    getStartOf(timelineMode),
    getEndOf(timelineMode)
  );
};

export default reduceEvents;



