<template>
  <div class="event-box--info">
    <div class="event-box--info--title">{{title}}</div>
    <div class="event-box--info--date" v-if="timelineMode !== 'day'">
      {{startDate|date}}<span class="event-box--info--date__end"> - {{endDate|date}}</span>
    </div>
    <!-- div class="event-box--info--overlay">
      <div class="event-box--info--overlay--body" v-html="event.description"></div>
      <event-social-buttons></event-social-buttons>
    </div -->
  </div>
</template>

<script>
  import EventSocialButtons from '../EventSocialButtons';
  import dateFormatter from '../../helpers/date-helper';

  function trim(html) {
    const div = document.createElement('div');
    div.innerHTML = html.substr(0, 60) + '...';
    return div.textContent || div.innerText || '';
  }

  export default {
    props: ['title', 'description', 'startDate', 'endDate'],
    components: {
      EventSocialButtons
    },
    computed: {
      timelineMode() {
        return this.$store.state.timelineMode;
      },
      trimEventDescription() {
        return trim(this.description);
      }
    },
    filters: {
      date(value) {
        return dateFormatter(new Date(value), 'DD.MM');
      }
    }
  };
</script>

<style>
  @import "variables.css";
  .event-box--info {
    padding: 0 .5rem;
    position: relative;
    margin-bottom:.5rem;
  }

  .event-box--info--title {
    color: #000;
    font-size: 1.2rem;
    line-height:1.2;
  }

  /* z-index for hover overlay*/
  .event-item .event-box--info--title {
    /*position: relative;*/
    /*z-index:25;*/
  }
/*
  .event-box--info--overlay {
    position: absolute;
    top:100%;
    left:0;
    margin-top:1px;
    background:#fff;
    width: 16rem;
    box-shadow: 0 0 5px rgba(0,0,0,.5);
    padding: 0 .5rem .2rem;
    box-sizing: border-box;
    display: none;
  }

  .event-box--info--overlay:before {
    position: absolute;
    top:-5px;
    left:0;
    background: #fff;
    width: 16rem;
    height:5px;
    content: '';
    display: none;
  }
*/
  .event-item__hover .event-box--info--overlay,
  .event-item__hover .event-box--info--overlay:before {
    display: block;
  }

  .event-box--info--date {
    color: #222;
    font-size: 1.1rem;
    font-weight: bold;
    z-index: 22;
  }

  .event-box--info--overlay i {
    width: 1.9rem;
    height: 1.9rem;
    line-height: 1.9rem;
  }

  .event-box--info--overlay--body {
    font-size: 1.1rem;
    color: #000;
    max-height: 5.2rem;
    overflow: hidden;
  }

  .event-box--info--overlay--body h2,
  .event-box--info--overlay--body h3,
  .event-box--info--overlay--body p,
  .event-box--info--overlay--body ul,
  .event-box--info--overlay--body li {
    display:inline;
    margin-right: .5em;
    font-size:1em;
    font-weight: normal
  }

  .event-box--info--overlay--body br {
    display: none;
  }

  .compact-mode .event-box--info--title {
    line-height: 1.1;
    font-size: 1.1rem;
    font-weight: 500;
    color: #16407d;
    word-wrap: break-word;
  }
/*
  .compact-mode .event-box--info--date__end,
  .compact-mode .event-box--info--overlay {
    display: none! important;
  }
  @media (--timeline-small) {
    .event-box--info--overlay {
      width: 14.5rem;
    }
    .event-box--info--overlay {
      display: none !important;
    }
  }
*/
</style>



