<template>
  <h3>{{categoryName|nameHyphenator}}</h3>
</template>

<script>
  import get from 'lodash/fp/get';
  import nameHyphenator from '../mixins/name-hyphenator-filter';

  const getName = get('name');

  export default {
    mixins: [nameHyphenator],
    props: ['categoryId'],
    computed: {
      category() {
        return this.$store.state.categories[this.categoryId] || undefined;
      },
      categoryName() {
        return this.category ? getName(this.category) : '';
      }
    }
  };
</script>



