<template>
  <div class="organization-info">
    <h3 class="organization-name">{{organizationName}}</h3>
    <p v-if="organizationDescription">{{organizationDescription}}</p>
  </div>
</template>

<style>
  .organization-info h3 {
    margin: 0 0 .7rem 0;
  }
  .organization-info p {
    margin: -0.2rem 0 .7rem 0;
    color: #666;
  }
</style>

<script>
  import get from 'lodash/fp/get';
  import memoize from 'lodash/memoize';

  const getName = memoize(get('name'), v => v.id);
  const getDescription = memoize(get('description'), v => v.id);

  export default {
    props: {
      organizationId: {
        default: undefined,
        validator(value) {
          return typeof value === 'number';
        }
      }
    },
    computed: {
      organization() {
        return this.$store.state.organizations[this.organizationId] || { id: 0 };
      },
      organizationName() {
        return getName(this.organization);
      },
      organizationDescription() {
        return getDescription(this.organization);
      }
    }
  };
</script>



