<template>
  <div class="form">
    <form v-on:submit.prevent id="form-login" name="form-login">
      <div v-if="!isLoggedIn">
        <ul v-if="errors.errors.length" class="form_errors">
          <li v-for="error in errors.errors">{{ error.msg }}</li>
        </ul>
        <label v-translate>fieldLoginName</label>
        <input type="text" id="loginform" name="login" v-model="login" v-validate.initial="login" data-rules="required|min:3" :class="{'has-error': errors.has('login') }" required>
        <label v-translate>fieldPassword</label>
        <input type="password" name="password" v-model="password" v-validate.initial="password" data-rules="required|min:3" :class="{'input': true, 'has-error': errors.has('password') }" required>
      </div>
      <button v-on:click.stop.prevent="submitAction" type="submit" v-bind:disabled="errors.errors.length > 0" class="button-primary" v-translate>Log in</button>
      <!--<google-sign-in-button></google-sign-in-button>-->
    </form>
  </div>
</template>


<style>
  @import "variables.css";

  .login .modal-container {
    max-width: 30rem;
    min-height: 23rem;
    height: auto;
  }

  .login .modal--header {
    background: #16407d;
  }

</style>


<script>
  import router from '../router/index';
  import notification from '../helpers/Notification';
  import GoogleSignInButton from './GoogleSignInButton';

  export default {
    data: function() {
      return {
        login: '',
        password: ''
      };
    },
    components: {
      GoogleSignInButton
    },
    computed: {
      routeParams() {
        return this.$store.state.route.params;
      },
      isLoggedIn() {
        return !!this.$store.state.user.username;
      }
    },
    methods: {
      close: function() {
        router.push({ name: 'date', params: this.$route.params });
      },
      submitAction: function(e) {
        this.$validator.validateAll();

        if (this.errors.any()) {
          e.preventDefault();
          return;
        }

        this.$store
          .dispatch('loginUserAndReturnPromise', {
            login: this.login,
            password: this.password
          })
          .then(() => {
            this.$emit('login');
            notification.success(this.t('LoggedIn'));
          })
          .catch(e => {
            var serverError = e.status === 409
              ? 'Incorrect user or password'
              : 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    }
  };
</script>



