<template>
  <header class="header" v-bind:class="{'header__menu-on': isMenuOn}" aria-labelledby="headerTitle">
    <button class="header_menu-toggle" v-on:click.prevent="toggleMenu"><i class="icon-menu"></i></button>
    <div class="header-wrapper">
      <router-link :to="{ name: 'date', params: todayParams}">
        <h1 class="header_logo"><span hidden id="headerTitle">NCFC Kalender</span></h1>
      </router-link>
      <calendar></calendar>
      <calendar-settings></calendar-settings>
      <event-filter></event-filter>
      <footer-buttons></footer-buttons>
    </div>
  </header>
</template>

<style>
  @import "variables.css";

  .header {
    background-color: var(--headerBgColor);
    box-sizing: border-box;
  }

  .ios .header {
    padding-top: 2rem;
    height: calc(2rem + var(--headerHeightSm));
  }

  .ios header.header__menu-on {
    height: auto;
  }

  .header-wrapper {
    height: 100vh;
    padding: .5rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    align-items: stretch;
  }

  .header-wrapper > * {
    order: 0;
    flex: 0 0 auto;
    align-self: auto;
  }

  .header-wrapper > *:nth-child(4) {
    order: 0;
    flex: 1 0 auto;
    align-self: auto;
  }

  .header_menu-toggle {
    position: fixed;
    overflow-y: auto;
    right: 0rem;
    width: 4.2rem;
    height: 4.2rem;
    margin: 0;
    padding: 0;
    border: none;
    background: none;
    line-height: 4rem;
    text-align: center;
    font-size: 2.4rem;
    color: #E9BE29;
    cursor: pointer;
    display: none;
  }

  .header_logo {
    background-image: url(../assets/img/ncf-logo-title.svg);
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    display: block;
    text-align: center;
    padding: 0;
    height: 11rem;
    width: 100%;
    margin: 2rem auto 2.5rem;
  }

  @media (--timeline-large) {
    .header {
      position: fixed;
      width: 20rem;
      height: 100%;
    }
  }

  @media (--timeline-small) {
    .header {
      height: var(--headerHeightSm);
      display: block;
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 100;
    }

    .header-wrapper {
      display: block;
      width: 300px;
      height: auto;
      margin-left: auto;
      margin-right: auto;
    }

    .header-wrapper > * {
      display: none;
    }

    .header-wrapper > *:nth-child(1) {
      display: block;
      position: relative;
      z-index: 1000;
    }

    .header_logo {
      margin: 0 auto;
      background-position: 50% 0;
      background-size: 14.25rem 10rem;
      width: 5rem;
      height: 6rem;
    }

    .header__menu-on {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1000;
      height: 100vh;
      overflow-y: auto;
    }

    .header__menu-on .header_menu-toggle {
      position: fixed;
      top: 0px;
      right: 0px;
    }

    .header__menu-on .header-wrapper {
      display: block;
      height: auto;
    }

    .header__menu-on .header-wrapper > * {
      display: block;
      padding-bottom: 2rem;
    }

    .header__menu-on .header-wrapper .header_logo {
      width: 100%;
      margin-top: 2rem;
      margin-bottom: 1rem;
      background-size: contain;
      height: 12rem;
    }

    .header_menu-toggle {
      display: inline-block;
    }

    .header__menu-on .header_menu-toggle i:before {
      content: '\e802';
    }
  }

  @media (--header-overflow) {
    .header {
      overflow-y: auto;
    }

    .header-wrapper {
      height: auto;
    }
  }
</style>

<script>
  import dateFormatter from '../helpers/date-helper';
  import EventFilter from './EventFilter';
  import Calendar from './Calendar/Calendar';
  import CalendarSettings from './Calendar/CalendarSettings';
  import FooterButtons from './Footer';

  export default {
    components: {
      EventFilter,
      Calendar,
      CalendarSettings,
      FooterButtons
    },
    data() {
      return {
        isMenuOn: false
      };
    },
    computed: {
      todayParams() {
        return {
          date: dateFormatter(this.$store.state.currentDate, 'YYYY-MM-DD')
        };
      }
    },
    methods: {
      toggleMenu() {
        this.isMenuOn = !this.isMenuOn;
      }
    }
  };
</script>



