import reduce from 'lodash/fp/reduce';
import sortBy from 'lodash/fp/sortBy';
import pickBy from 'lodash/fp/pickBy';
import pick from 'lodash/fp/pick';
import isNumber from 'lodash/fp/isNumber';
import map from 'lodash/fp/map';
import min from 'lodash/fp/min';
import max from 'lodash/fp/max';
import uniq from 'lodash/fp/uniq';
import filter from 'lodash/fp/filter';
import { createDate } from '../helpers/week-number';

function reducerCategoriesTree(result, value) {
  if (value.parentId === value.id) {
    result[value.id] = Object.assign(
      {},
      result[value.id] || { options: [] },
      value
    );
  } else {
    (result[value.parentId] ||
      (result[value.parentId] = { options: [] })).options
      .push(value);
  }
  return result;
}

const omitEmptyFilters = pickBy(isNumber);
const sortByStartDate = sortBy('startDate');
const getSimpleEvent = pick([
  'startDate',
  'endDate',
  'id',
  'categoryId',
  'organizationId',
  'levelId',
  'regionId',
  'type'
]);
const mapStartDate = map('startDate');
const getIds = map('id');

export default {
  routeDate: state => {
    'use strict';
    return createDate(state.route.params.date);
  },
  combinedFilteredEventsAndMeetings: state => {
    const events = filter(omitEmptyFilters(state.eventsFilter))(state.events);
    const meetings = filter(omitEmptyFilters(state.meetingsFilter))(
      state.meetings
    );
    if (state.meetingsFilter.categoryId) {
      return sortByStartDate([
        ...meetings.map(x => ({ ...x, type: 'meeting' }))
      ]);
    } else if (
      state.eventsFilter.categoryId ||
      state.eventsFilter.levelId ||
      state.eventsFilter.organizationId ||
      state.eventsFilter.regionId
    ) {
      return sortByStartDate([
        ...events.map(x => ({ ...getSimpleEvent(x), type: 'event' }))
      ]);
    } else {
      return sortByStartDate([
        ...events.map(x => ({ ...getSimpleEvent(x), type: 'event' })),
        ...meetings.map(x => ({ ...x, type: 'meeting' }))
      ]);
    }
  },
  startDatesOfFilteredActivities: state => {
    if (state.meetingsFilter.categoryId) {
      return uniq([
        ...filter(omitEmptyFilters(state.meetingsFilter))(state.meetings).map(
          x => x.startDate
        )
      ]).sort((a, b) => a - b);
    } else if (
      state.eventsFilter.categoryId ||
      state.eventsFilter.levelId ||
      state.eventsFilter.organizationId ||
      state.eventsFilter.regionId
    ) {
      return uniq([
        ...filter(omitEmptyFilters(state.eventsFilter))(state.events).map(
          x => x.startDate
        )
      ]).sort((a, b) => a - b);
    } else {
      return uniq([
        ...filter(omitEmptyFilters(state.eventsFilter))(state.events).map(
          x => x.startDate
        ),
        ...filter(omitEmptyFilters(state.meetingsFilter))(state.meetings).map(
          x => x.startDate
        )
      ]).sort((a, b) => a - b);
    }
  },
  outsideEvents: state => {
    const outsideTimestamps = mapStartDate(state.events);
    const leftTimestamp = min(outsideTimestamps);
    const rightTimestamp = max(outsideTimestamps);
    return {
      left: {
        timestamp: leftTimestamp,
        ids: getIds(filter(v => v.startDate === leftTimestamp)(state.events))
      },
      right: {
        timestamp: rightTimestamp,
        ids: getIds(filter(v => v.startDate === rightTimestamp)(state.events))
      }
    };
  },
  categoriesTree: state => {
    return reduce(reducerCategoriesTree, {})(state.categories);
  }
};



