import request from '../HttpRequest';
import defaultReject from '../HttpRequest/default-reject';
import keyBy from 'lodash/fp/keyBy';

const keyById = keyBy('id');

function RegionClass() {
  let loadedRegions;

  function getRegions() {
    if (!loadedRegions) {
      loadedRegions = loadRegions();
      return loadedRegions;
    }

    return getCachedRegions();
  }

  function loadRegions() {
    return request
      .get('/regions')
      .then(keyById)
      .then(setCachedRegions)
      .catch(defaultReject)
      .catch([]);
  }

  function setCachedRegions(data) {
    loadedRegions = data;
    return getCachedRegions();
  }

  function getCachedRegions() {
    return Promise.resolve(loadedRegions);
  }

  this.getAll = getRegions;
}

export default RegionClass;



