<template>
  <a v-bind:href="enrollmentLink" target="_blank" class="button button-primary" v-translate>Enroll</a>
</template>
<script>
  export default {
    name: 'EnrollmentLink',
    props: {
      enrollmentLink: {
        default: undefined,
        type: String
      }
    }
  };
</script>



