import spacetime from 'spacetime';
import AddToCalendarMixin from './add-to-calendar-mixin';

export const calendars = {
  google: {
    url: 'http://www.google.com/calendar/event?action=TEMPLATE&trp=false',
    parameters(title, location, details, start, end) {
      const parameters = {
        text: title,
        location: location,
        details: details
      };

      if (start && end) {
        parameters.dates = `${start}/${end}`;
      }

      return parameters;
    }
  },

  microsoft: {
    url: 'http://calendar.live.com/calendar/calendar.aspx?rru=addevent',
    parameters(title, location, details, start, end) {
      return {
        summary: title,
        location: location,
        description: details,
        dtstart: start,
        dtend: end
      };
    }
  }
};

export default {
  props: {
    /**
     * Event title.
     * @var string
     */
    title: {
      type: String,
      default: ''
    },

    /**
     * Event location.
     * @var string
     */
    location: {
      type: String,
      default: ''
    },

    /**
     * Event details.
     * @var string
     */
    details: {
      type: String,
      default: ''
    },

    /**
     * Event start.
     * @var date
     */
    start: {
      type: Date,
      default: null
    },

    /**
     * Event end.
     * @var date
     */
    end: {
      type: Date,
      default: null
    }
  },

  data() {
    return {
      /**
       * Available calendars.
       * @param object
       */
      calendars
    };
  },

  methods: {
    /**
     * Returns generated calendar url.
     *
     * @param calendar.
     */
    calendarUrl(calendar) {
      let url = this.calendars[calendar].url;
      const parameters = this.calendars[calendar].parameters(
        this.formatString(this.title),
        this.formatString(this.location),
        this.formatString(this.details),
        this.formatDate(this.start),
        this.formatDate(this.end)
      );

      for (const key in parameters) {
        if (parameters.hasOwnProperty(key) && parameters[key]) {
          url += `&${key}=${parameters[key]}`;
        }
      }

      return url;
    },

    formatString(string) {
      return encodeURIComponent(string).replace(/%20/g, '+');
    },

    formatDate(date) {
      return date ? date.toISOString().replace(/-|:|\.\d+/g, '') : null;
    }
  },

  mounted() {
    //
  },

  /**
   * Set component aliases for buttons and links.
   */
  components: {
    'google-calendar': {
      mixins: [AddToCalendarMixin],
      data: function() {
        return { calendar: 'google' };
      }
    },
    'microsoft-calendar': {
      mixins: [AddToCalendarMixin],
      data: function() {
        return { calendar: 'microsoft' };
      }
    },
    'ical-calendar': {
      mixins: [AddToCalendarMixin],
      data: function() {
        return { calendar: 'ical' };
      },
      mounted() {
        this.$el.setAttribute('download', `ical-${this.$parent.title}.ics`);
      },
      methods: {
        calendarUrl() {
          const { title, location, details, start, end } = this.$parent;
          const startDate = spacetime(start.getTime(), 'Europe/Oslo')
            .format('iso')
            .replace(/(-|:)/g, '')
            .slice(0, -4);
          const endDate = spacetime(end.getTime(), 'Europe/Oslo')
            .format('iso')
            .replace(/(-|:)/g, '')
            .slice(0, -4);

          const text = `BEGIN:VCALENDAR
VERSION:2.0
CALSCALE:GREGORIAN
BEGIN:VEVENT
SUMMARY:${title}
DTSTART;TZID=Europe/Oslo:${startDate}
DTEND;TZID=Europe/Oslo:${endDate}
DESCRIPTION:${details}
STATUS:CONFIRMED
SEQUENCE:3
END:VEVENT
END:VCALENDAR
`;
          return 'data:text/plain;charset=utf-8,' + encodeURIComponent(text);
        }
      }
    }
  }
};



