<template>
  <header v-bind:id="eventAttributeId" class="timeline_time-label" v-bind:class="{'timeline_time-label__past': isPast, 'timeline_time-label__current': isCurrent}" @click.prevent="goToDate()">
    <h3 v-if="subtitle">{{subtitle}}</h3>
    <h4>{{title}}</h4>
  </header>
</template>

<style>
  @import "variables.css";

  .timeline_time-label {
    padding:0;
    width:1px;
    position: relative;
    cursor: pointer;
  }

  .timeline_time-label:before {
    background: #fff;
    content: '';
    height: 11px;
    width: 11px;
    border: 2px solid #4a90e2;
    display: block;
    border-radius: 50%;
    float: left;
  }

  .timeline_time-label h3 {
    color: #5e6066;
    font-size: 1rem;
    font-weight: normal;
    line-height: 1rem;
    margin: 0;
    padding: .1rem 0 .5rem 1.8rem;
    white-space: nowrap;
  }

  .timeline_time-label h4 {
    font-size: 1.3rem;
    font-weight: normal;
    line-height: 1rem;
    padding: .6rem 0 0 1.8rem;
    margin: 0;
    white-space: nowrap;
  }

  .timeline_time-label__past:before {
    border-color: #62c6f1;
  }
  .timeline_time-label__past h4 {
    color: #5E6066;
  }

  .timeline_time-label__current:before {
    background: #e73e42;
    border-color: #fff;
  }

  @media (--timeline-large) {
    .timeline_time-label {
      margin-top: -var(--headerHeightLg);
      padding-top: 1.5rem;
      height:100vh;
    }
    .timeline_time-label:before {
      margin-top:9px;
    }
    .timeline_time-label:after {
      top:2.65rem;
    }
  }

  @media (--timeline-small) {
    .timeline_time-label {
      height:auto;
      width:100%;
      margin: 1rem 0 1.5rem -2.7rem;
      padding: 0 !important;
    }
    .timeline_time-label h3 {
      padding-left:2.7rem;
      padding-bottom:0;
    }
    .timeline_time-label h4 {
      padding-top:0.4rem;
      padding-left:2.7rem;
      font-weight: bold;
    }
    .timeline_time-label:before {
      margin-top:7px;
      margin-left:-1px;
    }
  }
</style>

<script>
  import fecha from 'fecha';
  import { createDate, weekNumber } from '../../helpers/week-number';
  import router from '../../router/index';

  function isCurrentDate(currentDate, date, mode) {
    date = new Date(date);
    currentDate = new Date(currentDate);
    switch (mode) {
      case 'year':
        return fecha.format(date, 'YYYY') === fecha.format(currentDate, 'YYYY');
      case 'month':
        return (
          fecha.format(date, 'YYYYMM') === fecha.format(currentDate, 'YYYYMM')
        );
      case 'week':
        return (
          weekNumber(date) === weekNumber(currentDate) &&
          fecha.format(date, 'YYYY') === fecha.format(currentDate, 'YYYY')
        );
      default:
        return (
          fecha.format(date, 'YYYYMMDD') === fecha.format(currentDate, 'YYYYMMDD')
        );
    }
  }

  function getSectionTitle(mode, date, i18n) {
    date = createDate(date);
    switch (mode) {
      case 'year':
        return fecha.format(date, 'YYYY'); // Year
      case 'month':
        return fecha.format(date, 'MMMM', i18n); // Month
      case 'week':
        return weekNumber(date); // Week 10
      default:
        return fecha.format(date, 'MMMM Do', i18n); // September, 26
    }
  }

  function getSectionSubtitle(mode, date, i18n) {
    date = createDate(date);
    switch (mode) {
      case 'year':
        return false; // Nothing here
      case 'month':
        return fecha.format(date, 'YYYY'); // Year
      case 'week':
        return fecha.format(date, 'MMMM', i18n); // September
      default:
        return weekNumber(date); // Week 10
    }
  }

  export default {
    props: ['event'],
    computed: {
      locale() {
        return (
          this.$translate &&
          this.$translate.locale &&
          this.$translate.locale.fecha
        );
      },
      title() {
        return (
          getSectionTitle(
            this.$store.state.timelineMode,
            this.event.startDate,
            this.locale
          ) +
          (this.$store.state.timelineMode === 'week' ? ' ' + this.t('Week') : '')
        );
      },
      subtitle() {
        return (
          getSectionSubtitle(
            this.$store.state.timelineMode,
            this.event.startDate,
            this.locale
          ) +
          (this.$store.state.timelineMode === 'day' ? ' ' + this.t('Week') : '')
        );
      },
      isPast() {
        return (
          !this.isCurrent && this.event.startDate < this.$store.state.currentDate
        );
      },
      isCurrent() {
        return isCurrentDate(
          this.$store.state.currentDate,
          this.event.startDate,
          this.$store.state.timelineMode
        );
      },
      eventAttributeId() {
        return 'tl-header' + fecha.format(this.event.startDate, 'YYYYMMDD');
      }
    },
    methods: {
      goToDate() {
        router.push({
          name: 'date',
          params: {
            ...this.$route.params,
            date: fecha.format(this.event.startDate, 'YYYY-MM-DD')
          }
        });
      }
    }
  };
</script>



