<template>
  <ul class="event-class-list"><li v-for='klass in list'>{{klass.name}}</li></ul>
</template>
<style>
  .event-class-list:empty {
    display: none;
  }
  .event-class-list {
    list-style-type: none;
    display: block;
    font-size:.85em;
    margin: 2em 0 0 0;
    padding: 0;
  }
  .event-class-list li {
    display: inline-block;
    margin: 0 5px 0 0;
    padding: 0 5px;
    border: 1px solid #7c8a95;
    background: #7c8a95;
    color: #fff;
    border-radius: 3px;
  }
</style>
<script>
  export default {
    props: ['list']
  };
</script>



