<template>
  <div class="toggle-calendar">
    <input type="checkbox" name="toggle-calendar" id="toggleCalendar" class="filter-toggle-checkbox">
    <label for="toggleCalendar"><i class="icon icon-calendar"></i></label>
    <div class="calendar-view">
      <div class="calendar-wrapper">
        <calendar></calendar>
      </div>
    </div>
  </div>
</template>

<style>
  @import "variables.css";

  #toggleCalendar:checked {
    display: none;
  }
  #toggleCalendar:checked + label + .calendar-view {
    display: block;
  }
  .filter-toggle-checkbox {
    display: none;
  }
  #toggleCalendar:checked + label > i.icon-calendar:before {
    content: '\E802';
  }
  #toggleCalendar:checked + label > .icon.icon-calendar {
    border-color: transparent;
    background: transparent;
  }

  .calendar-view {
    width:100%;
    height: 100%;
    background: #fff;
    top:0;
    display: none;
    z-index: 904;
    position: absolute;
  }

  .icon-calendar {
    position: relative;
    z-index:900;
  }

  @media (--timeline-small) {
    #toggleCalendar:checked + label > .icon.icon-calendar {
      position: absolute;
      top:0;
      right:0;
      left: auto;
    }
  }

  @media (--timeline-large) {
    .calendar-view {
      top:1rem;
      height:auto;
    }
    #toggleCalendar:checked + label > .icon.icon-calendar {
      left:31rem;
    }
  }

  @media (--header-large) {
     .toggle-calendar {
       display: none;
     }
  }

  @media (--header-medium) {
     .toggle-calendar {
       display: none;
     }
  }

</style>

<script>
  import Calendar from './Calendar/Calendar.vue';

  export default {
    components: {
      Calendar
    }
  };
</script>



