<template>
  <div class="calendar-settings">
    <label for="settingsToggle">
      <input type="checkbox" id="settingsToggle" v-model="checked" class="filter-toggle-checkbox">
      <div class="calendar-settings_list">
        <settings-calendar-period></settings-calendar-period>
        <settings-calendar-mode></settings-calendar-mode>
      </div>
      <span>{{settingsTitle}}</span>
    </label>
  </div>
</template>

<script>
  import dateFormatter from '../../helpers/date-helper';
  import SettingsCalendarPeriod from './SettingsCalendarPeriod';
  import SettingsCalendarMode from './SettingsCalendarMode';

  export default {
    data: function() {
      return {
        checked: false
      };
    },
    components: {
      SettingsCalendarPeriod,
      SettingsCalendarMode
    },
    computed: {
      settingsShow() {
        return this.t('Show settings');
      },
      settingsHide() {
        return this.t('Hide settings');
      },
      settingsTitle: function() {
        return this.checked === false ? this.settingsShow : this.settingsHide;
      }
    }
  };
</script>

<style>
  @import "variables.css";

  .calendar-settings {
    height: 8rem;
  }

  .calendar-settings_list {
    overflow: hidden;
    display: none;
  }

  #settingsToggle:checked + .calendar-settings_list {
    display: block;
  }

  #settingsToggle + .calendar-settings_list + span {
    color: #fff;
    font-size: 1.2rem;
    margin-left: .2rem;
    border-bottom: 1px dashed #fff;
    cursor: pointer;
  }

  #settingsToggle:checked + .calendar-settings_list + span {
    opacity: 0.4;
  }

  @media (--timeline-small) {
    .calendar-settings {
      width: 31rem;
      margin: 0 auto;
    }
  }
</style>



