  import notification from '../helpers/Notification';

  export default {
    data() {
      return {
        googleSignInParams: {
          client_id: '992366419202-fkff9jpk23685la8tmol60ueclndmutb.apps.googleusercontent.com'
        }
      };
    },
    methods: {
      onSignInSuccess(googleUser) {
        // Full information check on Google API https://developers.google.com/identity/sign-in/web/reference#users
        User.googleSignIn(googleUser.getAuthResponse().id_token).then(data => {
          console.log(data);
 ///         this.$store.dispatch('loginUserGoogleWay', data)
//            .then(() => {
//              notification.success(this.t('LoggedIn'));
//            })
//            .catch(e => {
//              notification.error(this.t('Login fail'));
//            });
        });

//        GoogleAuth.grantOfflineAccess({
//          redirect_uri: 'postmessage'
//        }).then(response => {
//          User.googleSignIn(response.code).then(data => {
//            this.$store
//              .dispatch('loginUserGoogleWay', data)
//              .then(() => {
//                notification.success(this.t('LoggedIn'));
//              })
//              .catch(e => {
//                notification.error(this.t('Login fail'));
//              });
//          });
//        });
      },
      onSignInError(error) {
        console.log('Google request error!', error);
      }
    }
  };
</script>

<style>
  .g-signin-button {
    box-sizing: border-box;
    position: relative;
    padding: 0 15px 0 46px;
    margin-top: 10px;
    border: none;
    text-align: left;
    line-height: 38px;
    white-space: nowrap;
    border-radius: 0.2em;
    font-size: 14px;
    color: #FFF;
    background: #DD4B39;
    cursor: pointer;
  }

  .g-signin-button:before {
    box-sizing: border-box;
    position: absolute;
    top: 8px;
    left: 3px;
    font-size: 23px;
    font-family: "calendar";
    font-style: normal;
    font-weight: normal;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    width: 34px;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: '\e808';
  }
  .g-signin-button:focus {
    outline: none;
    background: #E74B37;
  }
  .g-signin-button:active {
    box-shadow: inset 0 0 0 32px rgba(0,0,0,0.1);
  }

</style>



