import request from '../HttpRequest';
import defaultReject from '../HttpRequest/default-reject';
import keyBy from 'lodash/fp/keyBy';

const keyById = keyBy('id');

function LevelClass() {
  let loadedLevels;

  function getLevels() {
    if (!loadedLevels) {
      loadedLevels = loadLevels();
      return loadedLevels;
    }

    return getCachedLevels();
  }

  function loadLevels() {
    return request
      .get('/levels')
      .then(keyById)
      .then(setCachedLevels)
      .catch(defaultReject);
  }

  function setCachedLevels(data) {
    loadedLevels = data;
    return getCachedLevels();
  }

  function getCachedLevels() {
    return Promise.resolve(loadedLevels);
  }

  this.getAll = getLevels;
}

export default LevelClass;



