var eventTitles = {
  treningssamlinger: 'Trenings\u00ADsamlinger',
  pressekonferanser: 'Presse\u00ADkonferanser',
  'uci kongress': 'Uci\u00AD kongress'
};

function eventHyphenator(name) {
  return eventTitles[name.toLowerCase()] || name;
}

const nameHyphenator = {
  filters: {
    nameHyphenator: function(value) {
      return eventHyphenator(value);
    }
  }
};

export default nameHyphenator;



