import request from '../HttpRequest';

function SubscriptionClass() {
  function saveSubscription(data) {
    return request.post(
      '/enrollments/' + data.eventId + '/users/' + data.userId
    );
  }

  this.saveSubscription = saveSubscription;
}

export default SubscriptionClass;



