<template>
  <div id="app" lang="no">
    <app-header></app-header>
    <timeline></timeline>
    <router-view></router-view>
  </div>
</template>

<script>
  require('../assets/css/fonts.css');
  require('../assets/css/main.css');
  require('../assets/css/forms.css');

  import {
    LOAD_FILTER_CATEGORIES,
    LOAD_FILTER_ORGANIZATIONS,
    LOAD_FILTER_REGIONS,
    LOAD_FILTER_LEVELS,
    LOAD_FILTER_MEETING_CATEGORIES
  } from '../store/types';
  import AppHeader from '../components/Sidebar';
  import Timeline from './Timeline/Timeline';

  export default {
    created() {
      setInterval(() => {
        this.$store.dispatch('getCurrentUser');
      }, 1700000);

      Promise.all([
        this.$store.dispatch(LOAD_FILTER_CATEGORIES),
        this.$store.dispatch(LOAD_FILTER_ORGANIZATIONS),
        this.$store.dispatch(LOAD_FILTER_REGIONS),
        this.$store.dispatch(LOAD_FILTER_LEVELS),
        this.$store.dispatch(LOAD_FILTER_MEETING_CATEGORIES),
        this.$store.dispatch('loadMeetings')
      ]).then(
        () => {
          this.hideAppLoader();
        },
        () => {
          this.hideAppLoader();
        }
      );
    },
    components: {
      AppHeader,
      Timeline
    },
    mounted() {
      this.$translate.setLang('no');
    },
    methods: {
      hideAppLoader() {
        document.getElementById('loader').className += ' app-loader--off';
      }
    }
  };
</script>



