import dateFormatter from './date-helper';

function generateCalendarDaysObject(date) {
  const currentYear = date.getFullYear();
  const currentMonth = date.getMonth();

  const prevMonthDate = new Date(currentYear, currentMonth);
  const nextMonthDate = new Date(currentYear, currentMonth + 1);

  const firstDayInCurrentMonth =
    (new Date(currentYear, currentMonth, 1).getDay() + 6) % 7;
  const lastDayInCurrentMonth =
    (new Date(currentYear, currentMonth + 1, 0).getDay() + 6) % 7;

  let start = new Date(currentYear, currentMonth, 1 - firstDayInCurrentMonth);
  const end = new Date(
    currentYear,
    currentMonth + 1,
    6 - lastDayInCurrentMonth,
    23,
    59,
    59
  );

  const calendarObj = [];

  while (start < end) {
    const dateKey = dateFormatter(start, 'YYYY-MM-DD');

    calendarObj.push({
      day: dateFormatter(start, 'D'),
      dateKey: dateKey,
      date: start,
      prevClass: prevMonthDate > start,
      nextClass: nextMonthDate <= start
    });

    const newDate = new Date(start);
    newDate.setDate(newDate.getDate() + 1);
    start = newDate;
  }

  return calendarObj;
}

export default generateCalendarDaysObject;



