<style>
  @import "variables.css";

  .timeline_period {
    margin: 0;
    padding: 0;
    width: 20px;
    transition: width 0.3s ease;
  }
  .timeline_period__empty .timeline_period--notification {
    text-align: center;
    margin-top: 24px;
    font-family: var(--baseHeaderFontFamily);
    color: #999;
  }

  @media (--timeline-large) {
    .timeline_period {
      margin-top: -var(--headerHeightLg);
      height:100vh;
    }
    .timeline_period__empty {
      width: 100%;
    }
    .timeline_period__limit-left {
      width: 20px;
      /*background: linear-gradient(to right, rgba(0,0,0,.25) 0%, rgba(0,0,0,.07) 20%, rgba(0,0,0,0) 50%);*/
    }
    .timeline_period__limit-right {
      width: 20px;
      /*background: linear-gradient(to left, rgba(0,0,0,.25) 0%, rgba(0,0,0,.07) 20%, rgba(0,0,0,0) 50%);*/
    }
  }

  @media (--timeline-small) {
    .timeline_period {
      height:auto;
      width:100%;
      margin: 1rem 0 1rem -2.7rem;
      padding: 0 !important;
    }
  }
</style>

<script>
  import { createDate } from '../../helpers/week-number';
  import TimelineCurrentDate from './TimelineCurrentDate';
  export default {
    props: ['event'],
    components: {
      TimelineCurrentDate
    },
    computed: {
      isLimitLeft() {
        return (
          this.event.startDate === -Infinity &&
          this.event.endDate !== Infinity &&
          this.$store.state.eventsStatus.limitLeft
        );
      },
      isLimitRight() {
        return (
          this.event.startDate !== -Infinity &&
          this.event.endDate === Infinity &&
          this.$store.state.eventsStatus.limitRight
        );
      },
      isEmpty() {
        return (
          this.event.startDate === -Infinity &&
          // this.$store.state.eventsStatus.limitLeft &&
          // this.$store.state.eventsStatus.limitRight &&
          this.event.endDate === Infinity
        );
      },
      isLimit() {
        return this.isLimitLeft || this.isLimitRight;
      },
      isLoading() {
        return (
          (this.event.startDate === -Infinity &&
            !this.$store.state.eventsStatus.limitLeft &&
            this.$store.state.eventsStatus.loadingLeft) ||
          (this.event.endDate === Infinity &&
            !this.$store.state.eventsStatus.limitRight &&
            this.$store.state.eventsStatus.loadingRight)
        );
      },
      currentDate() {
        return createDate(this.$store.state.currentDate);
      },
      isCurrentPeriod() {
        return (
          this.currentDate >= this.event.startDate &&
          this.event.endDate >= this.currentDate
        );
      }
    },
    methods: {
      innerElements(h) {
        const elements = [];
        if (this.isCurrentPeriod) {
          elements.push(h(TimelineCurrentDate));
        }
        if (this.isEmpty) {
          elements.push(
            h(
              'h2',
              { class: { 'timeline_period--notification': true } },
              this.t('NoEventsScheduled')
            )
          );
        }
        return elements;
      }
    },
    render(h) {
      return h(
        'div',
        {
          class: {
            timeline_period: true,
            timeline_period__loading: this.isLoading,
            timeline_period__empty: this.isEmpty,
            timeline_period__limit: this.isLimit,
            'timeline_period__limit-left': this.isLimitLeft,
            'timeline_period__limit-right': this.isLimitRight
          }
        },
        [this.innerElements(h)]
      );
    }
  };
</script>



