function errorsDictionary() {
  const dictionary = {
    en: {
      messages: {
        confirmed: function() {
          return 'The Password is not confirmed.';
        }
      }
    },
    no: {
      messages: {
        confirmed: function() {
          return 'Passordet stemmer ikke.';
        }
      }
    }
  };
  return dictionary;
}

export default errorsDictionary;



