import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import debounce from 'lodash/fp/debounce';
import isEmpty from 'lodash/fp/isEmpty';
import mapValues from 'lodash/fp/mapValues';

import db from '../helpers/Db';
import oneByOne from '../helpers/one-by-one';
import combineEvents from '../helpers/EventsHelper/combine-events';
import notification from '../helpers/Notification';

import {
  LOAD_FILTER_CATEGORIES,
  LOAD_FILTER_ORGANIZATIONS,
  LOAD_FILTER_REGIONS,
  LOAD_FILTER_LEVELS,
  LOAD_FILTER_MEETING_CATEGORIES
} from './types';

Vue.use(Vuex);

const setLoaderOn = commit => {
  'use strict';
  commit('SET_PROCESSING_ON');
};

const setLoaderOff = debounce(500)(commit => {
  commit('SET_PROCESSING_OFF');
});

import getters from './getters';
import state from './state';

const mutations = {
  SET_PROCESSING_ON(state) {
    state.isProcessing = true;
  },
  SET_PROCESSING_OFF(state) {
    state.isProcessing = false;
  },
  SWITCH_MODE(state) {
    state.displayModeCompact = !state.displayModeCompact;
  },
  SWITCH_TIMELINE_MODE(state, data) {
    state.timelineMode = data;
  },
  SWITCH_LOADING_EVENTS_LEFT_ON(state) {
    state.eventsStatus.loadingLeft = true;
  },
  SWITCH_LOADING_EVENTS_LEFT_OFF(state) {
    state.eventsStatus.loadingLeft = false;
  },
  SWITCH_LOADING_EVENTS_RIGHT_ON(state) {
    state.eventsStatus.loadingRight = true;
  },
  SWITCH_LOADING_EVENTS_RIGHT_OFF(state) {
    state.eventsStatus.loadingRight = false;
  },
  SCROLL_TO_DATE_LEFT(state, data) {
    state.eventsStatus.scrollToDateLeft = data;
  },
  SCROLL_TO_DATE_RIGHT(state, data) {
    state.eventsStatus.scrollToDateRight = data;
  },
  [LOAD_FILTER_CATEGORIES]: (state, data) => {
    state.categories = data;
  },
  [LOAD_FILTER_REGIONS]: (state, data) => {
    state.regions = data;
  },
  [LOAD_FILTER_ORGANIZATIONS]: (state, data) => {
    state.organizations = data;
  },
  [LOAD_FILTER_LEVELS]: (state, data) => {
    state.levels = data;
  },
  [LOAD_FILTER_MEETING_CATEGORIES]: (state, data) => {
    state.categoriesOfMeetings = data;
  },
  LOAD_MEETINGS(state, data) {
    state.meetings = data;
  },
  LOAD_MEETING(state, data) {
    state.meetings = { ...state.meetings, [data.id]: data };
  },
  COMBINE_EVENTS_RIGHT(state, data) {
    state.events = combineEvents.right(state.events, data);
  },
  COMBINE_EVENTS_LEFT(state, data) {
    state.events = combineEvents.left(state.events, data);
  },
  SET_EVENTS_LEFT_LIMIT(state, date) {
    state.eventsStatus.limitLeft = date;
  },
  SET_EVENTS_RIGHT_LIMIT(state, date) {
    state.eventsStatus.limitRight = date;
  },
  LOAD_EVENT(state, data) {
    state.events = { ...state.events, [data.id]: data };
  },
  FILTER_BY_ORGANIZATION(state, data) {
    state.eventsFilter.organizationId = data || false;
  },
  FILTER_BY_LEVELS(state, data) {
    state.eventsFilter.levelId = data || false;
  },
  FILTER_BY_REGIONS(state, data) {
    state.eventsFilter.regionId = data || false;
  },
  FILTER_BY_CATEGORY(state, data) {
    state.eventsFilter.categoryId = data || false;
  },
  FILTER_BY_MEETING(state, data) {
    state.meetingsFilter.categoryId = data || false;
  },
  RESET_FILTERS(state) {
    state.eventsFilter = mapValues(() => {
      return false;
    })(state.eventsFilter);
    state.meetingsFilter = mapValues(() => {
      return false;
    })(state.meetingsFilter);
    state.showMeetingsFilter = true;
    state.showEventFilter = true;
  },
  HIDE_FILTERS(state, data) {
    if (data) {
      state.showEventFilter = true;
      state.showMeetingsFilter = false;
    } else {
      state.showEventFilter = false;
      state.showMeetingsFilter = true;
    }
  },
  SET_USER(state, data) {
    state.user = data;
  },
  UPDATE_USER_PROFILE(state, data) {
    state.user.userProfile = data;
  }
};

const actions = {
  setProcessingOn({ commit }) {
    commit('SET_PROCESSING_ON');
  },
  setProcessingOff({ commit }) {
    commit('SET_PROCESSING_OFF');
  },
  [LOAD_FILTER_CATEGORIES]: ({ commit }) => {
    return db.Categories.getAll().then(data => {
      commit(LOAD_FILTER_CATEGORIES, data);
    });
  },
  [LOAD_FILTER_ORGANIZATIONS]: ({ commit }) => {
    return db.Organizations.getAll().then(data => {
      commit(LOAD_FILTER_ORGANIZATIONS, data);
    });
  },
  [LOAD_FILTER_REGIONS]: ({ commit }) => {
    return db.Regions.getAll().then(data => {
      commit(LOAD_FILTER_REGIONS, data);
    });
  },
  [LOAD_FILTER_LEVELS]: ({ commit }) => {
    return db.Levels.getAll().then(data => {
      commit(LOAD_FILTER_LEVELS, data);
    });
  },
  [LOAD_FILTER_MEETING_CATEGORIES]: ({ commit }) => {
    return db.Meetings.getCategories().then(data => {
      commit('LOAD_FILTER_MEETING_CATEGORIES', data);
    });
  },
  loadMeetings({ commit }) {
    return db.Meetings.getAll().then(data => {
      commit('LOAD_MEETINGS', data);
    });
  },
  loadMeeting({ commit }, data) {
    return db.Meetings.getById(data).then(meeting => {
      commit('LOAD_MEETING', meeting);
    });
  },
  loadEventsLeft({ commit }, data) {
    oneByOne(() => {
      commit('SWITCH_LOADING_EVENTS_LEFT_ON');
      return db.Events
        .getEarlierThan(data.date, data.except)
        .then(result => {
          if (isEmpty(result)) {
            commit('SET_EVENTS_LEFT_LIMIT', data.date);
          }
          commit('COMBINE_EVENTS_LEFT', result);
          commit('SWITCH_LOADING_EVENTS_LEFT_OFF');
        })
        .catch(() => {
          'use strict';
          commit('SWITCH_LOADING_EVENTS_LEFT_OFF');
        });
    });
  },
  loadEventsRight({ commit }, data) {
    oneByOne(() => {
      commit('SWITCH_LOADING_EVENTS_RIGHT_ON');
      return db.Events
        .getLaterThan(data.date, data.except)
        .then(result => {
          if (isEmpty(result)) {
            commit('SET_EVENTS_RIGHT_LIMIT', data.date);
          }
          commit('COMBINE_EVENTS_RIGHT', result);
          commit('SWITCH_LOADING_EVENTS_RIGHT_OFF');
        })
        .catch(() => {
          'use strict';
          commit('SWITCH_LOADING_EVENTS_RIGHT_OFF');
        });
    });
  },
  loadEvent({ commit }, data) {
    return db.Events.getById(data).then(event => {
      commit('LOAD_EVENT', event);
    });
  },
  saveSubscription({ commit }, data) {
    setLoaderOn(commit);
    return db.Subscriptions
      .saveSubscription(data)
      .then(event => {
        setLoaderOff(commit);
        return event;
      })
      .catch(e => {
        setLoaderOff(commit);
        if (e.message) {
          notification.error(e.message);
        }
        return Promise.reject(e);
      });
  },
  switchMode({ commit }) {
    commit('SWITCH_MODE');
  },
  switchTimelineMode({ commit }, mode) {
    commit('SWITCH_TIMELINE_MODE', mode);
  },
  switchFilterByOrganization({ commit }, mode) {
    commit('FILTER_BY_ORGANIZATION', mode);
  },
  switchFilterByLevel({ commit }, mode) {
    commit('FILTER_BY_LEVELS', mode);
  },
  switchFilterByRegion({ commit }, mode) {
    commit('FILTER_BY_REGIONS', mode);
  },
  switchFilterByMeeting({ commit }, mode) {
    commit('FILTER_BY_MEETING', mode);
  },
  switchFilterByCategory({ commit }, mode) {
    commit('FILTER_BY_CATEGORY', mode);
  },
  resetFilters({ commit }) {
    commit('RESET_FILTERS');
  },
  hideFilter({ commit }, data) {
    commit('HIDE_FILTERS', data);
  },
  loginUserAndReturnPromise({ commit }, data) {
    setLoaderOn(commit);
    return db.User
      .login(data.login, data.password)
      .then(user => {
        commit('SET_USER', user);
        setLoaderOff(commit);
      })
      .catch(e => {
        setLoaderOff(commit);
        return Promise.reject(e);
      });
  },
  loginUserGoogleWay({ commit }, data) {
    commit('SET_USER', data);
  },
  registerUserAndReturnPromise({ commit }, data) {
    setLoaderOn(commit);
    return db.User
      .register({
        username: data.username,
        password: data.password,
        enabled: true,
        userProfile: {
          firstName: data.firstName,
          lastName: data.lastName,
          maleGender: data.maleGender,
          phone: data.phone,
          birthday: new Date(data.birthday).getTime()
        }
      })
      .then(() => {
        return db.User.login(data.username, data.password);
      })
      .then(user => {
        commit('SET_USER', user);
        setLoaderOff(commit);
      });
  },
  updateProfile({ commit, state }, data) {
    setLoaderOn(commit);
    const prepUserProfileData = {
      firstName: data.firstName,
      lastName: data.lastName,
      maleGender: data.maleGender,
      phone: data.phone,
      id: state.user.userProfile.id,
      birthday: new Date(data.birthday).getTime()
    };
    commit('UPDATE_USER_PROFILE', prepUserProfileData);
    return db.User.updateProfile(state.user).then(() => setLoaderOff(commit));
  },
  logoutUser({ commit }) {
    setLoaderOn(commit);
    db.User
      .logout()
      .then(() => {
        setLoaderOff(commit);
        commit('SET_USER', { userProfile: {} });
      })
      .catch(e => {
        setLoaderOff(commit);
        return e;
      });
  },
  getCurrentUser({ commit }) {
    db.User
      .getCurrentUser()
      .then(user => {
        commit('SET_USER', user);
      })
      .catch(() => {
        commit('SET_USER', {});
      });
  }
};

const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  strict: true, // process.env.NODE_ENV === 'production',
  plugins: [
    createPersistedState({
      key: 'ncfc-java',
      paths: Object.keys(state).filter(
        v =>
          v !== 'events' &&
          v !== 'meetings' &&
          v !== 'eventsStatus' &&
          v !== 'currentDate'
      )
    })
  ]
});

export default store;



