export function getBoundingData(element) {
  const rect = element.getBoundingClientRect();
  return {
    top: rect.top,
    right: rect.right,
    bottom: rect.bottom,
    left: rect.left,
    width: rect.width,
    height: rect.height,
    scrollWidth: element.scrollWidth,
    scrollHeight: element.scrollHeight
  };
}

export function getScrollData(element) {
  return {
    scrollTop: element.scrollTop,
    scrollLeft: element.scrollLeft
  };
}

export function isRectangleInViewport(rectangle, viewport, cushion) {
  'use strict';
  cushion = +cushion || 0;
  viewport = {
    top: viewport.top - cushion,
    left: viewport.left - cushion,
    bottom: viewport.bottom + cushion,
    right: viewport.right + cushion
  };

  return (
    !!rectangle &&
    rectangle.bottom <= viewport.bottom &&
    rectangle.right <= viewport.right &&
    rectangle.top >= viewport.top &&
    rectangle.left >= viewport.left
  );
}

export function isInViewport(viewport, el, cushion) {
  'use strict';
  return isRectangleInViewport(getBoundingData(el), viewport, cushion);
}



