<template>
  <div class="gmap">
    <div class="gmap-js" id="gmap"></div>
    <a target="_blank" class="gmap-image" id="gmap-image" v-bind:href="mapLink" v-bind:style="mapImageStyle"></a>
  </div>
</template>

<script>
  import GoogleMapsLoader from '../../helpers/GoogleMaps/index';
  import GMaps from 'gmaps';

  export default {
    props: ['event'],
    data: function() {
      return {
        map: undefined,
        mapImageSrc: undefined,
        google: undefined,
        gmapSettings: {
          div: '#gmap',
          zoom: this.event.mapZoom || 8,
          lat: this.event.latitude,
          lng: this.event.longitude,
          scrollwheel: false,
          panControl: false,
          mapTypeControl: false,
          overviewMapControl: false,
          streetViewControl: false
        }
      };
    },
    computed: {
      mapLink() {
        return (
          'http://www.google.com/maps/place/' +
          this.event.latitude +
          ',' +
          this.event.longitude +
          '/@' +
          this.event.latitude +
          ',' +
          this.event.longitude +
          ',' +
          this.event.mapZoom +
          'z'
        );
      },
      mapImageStyle() {
        if (!this.mapImageSrc) {
          return {};
        }

        return {
          backgroundImage: 'url("' + this.mapImageSrc + '")'
        };
      }
    },
    mounted() {
      GoogleMapsLoader.load(google => {
        this.google = google;
        this.map = new GMaps(this.gmapSettings);

        this.map.setCenter({
          lat: this.event.latitude,
          lng: this.event.longitude
        });

        this.map.addMarker({
          lat: this.event.latitude,
          lng: this.event.longitude
        });

        this.mapImageSrc = GMaps.staticMapURL({
          zoom: this.event.mapZoom || 8,
          lat: this.event.latitude,
          lng: this.event.longitude,
          scale: 2,
          size: [512, 512],
          markers: [{ lat: this.event.latitude, lng: this.event.longitude }]
        });
      });
    }
  };
</script>


<style>
  @import "variables.css";

  .gmap {
    position: relative;
    padding-bottom:66%;
    margin-bottom: 1rem;
  }

  .gmap-js, .gmap-image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .gmap-image {
    background-position: 50% 50%;
    background-size: 512px 512px;
    background-repeat: no-repeat;
    display: none;
  }

  @media (--timeline-small) {
    .gmap-js {
      display: none;
    }

    .gmap-image {
      display: block;
    }
  }
</style>



