import Vue from 'vue';
import Router from 'vue-router';
import dateFormatter from '../helpers/date-helper';
import EventView from '../components/EventView/EventView';
import MeetingView from '../components/MeetingView/MeetingView';
import LoginPopup from '../components/LoginPopup';
import RegisterPopup from '../components/RegisterPopup';
import store from '../store';

Vue.use(Router);

const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/'
    },
    {
      path: '/:date',
      name: 'date'
    },
    {
      path: '/:date/login',
      component: LoginPopup,
      name: 'login'
    },
    {
      path: '/:date/register',
      component: RegisterPopup,
      name: 'register'
    },
    {
      path: '/:date/user/edit',
      component: RegisterPopup,
      name: 'useredit'
    },
    {
      path: '/:date/event/:id',
      component: EventView,
      name: 'viewEvent',
      meta: {
        loadEvent: true
      }
    },
    {
      path: '/:date/meeting/:id',
      component: MeetingView,
      name: 'viewMeeting',
      meta: {
        loadMeeting: true
      }
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (
    !to.params.date ||
    !to.params.date.match(/^\d{4}-\d{2}-\d{2}$/g) ||
    String(new Date(to.params.date)) === 'Invalid Date'
  ) {
    next('/' + dateFormatter(new Date(), 'YYYY-MM-DD'));
  } else {
    next();
  }
});

router.beforeEach((to, from, next) => {
  if (to.meta.loadEvent === true) {
    if (!store.state.events[to.params.id]) {
      store.dispatch('loadEvent', to.params.id);
    }
  }
  next();
});

router.beforeEach((to, from, next) => {
  if (to.meta.loadMeeting === true) {
    if (!store.state.meetings[to.params.id]) {
      store.dispatch('loadMeeting', to.params.id);
    }
  }
  next();
});

export default router;



