<template>
  <div>
    <h2 v-translate>Time, Location, Map</h2>
    <p v-if="event.registrationDate">
      <span v-translate>Registration date</span>: {{newDateFormat(event.registrationDate)}}
    </p>
    <p v-if="event.startDate">
      <span v-translate>Start date</span>: {{newDateFormat(event.startDate)}}
    </p>
    <p v-if="event.endDate">
      <span v-translate>End date</span>: {{newDateFormat(event.endDate)}}
    </p>

    <event-view-map v-if="event.latitude && event.longitude" v-bind:event="event"></event-view-map>
    <event-view-autocomplete v-if="event.latitude && event.longitude" v-bind:event="event"></event-view-autocomplete>
    <div class="editor-text" v-html="event.geoDescription"></div>

  </div>
</template>

<script>
  import dateFormatter from '../../helpers/date-helper';
  import EventViewMap from './EventViewMap';
  import EventViewAutocomplete from './EventViewAutocomplete';

  export default {
    props: ['event'],
    components: {
      EventViewMap,
      EventViewAutocomplete
    },
    methods: {
      newDateFormat(data) {
        return dateFormatter(data, 'DD.MM.YYYY HH:ss');
      }
    }
};
</script>



