<template>
  <header class="calendar_header">
    <calendar-navigation-dropdown v-bind:date="date" @change="navigate"></calendar-navigation-dropdown>
    <button type="button" v-bind:disabled="isDisabledPrev" @click="goPrevious" class="btn-prev">
      <i class="icon-left-dir"></i>
    </button>
    <button type="button" v-bind:disabled="isDisabledNext" @click="goNext" class="btn-next">
      <i class="icon-right-dir"></i>
    </button>
  </header>
</template>

<script>
  import filter from 'lodash/fp/filter';
  import min from 'lodash/fp/min';
  import max from 'lodash/fp/max';
  import map from 'lodash/fp/map';
  import uniq from 'lodash/fp/uniq';
  import CalendarNavigationDropdown from './CalendarNavigationDropdown';

  const mapStartDate = map('startDate');

  export default {
    props: ['date'],
    components: {
      CalendarNavigationDropdown
    },
    data() {
      return {
        isClickedPrevMonth: false,
        isClickedNextMonth: false
      };
    },
    computed: {
      year() {
        return this.date.getFullYear();
      },
      month() {
        return this.date.getMonth();
      },
      prevMonthEvent() {
        return new Date(this.year, this.month - 1, 1).getTime();
      },
      nextMonthEvent() {
        return new Date(this.year, this.month + 1, 1).getTime();
      },
      isDisabledPrev() {
        return (
          this.isClickedPrevMonth || this.$store.state.eventsStatus.loadingLeft
        );
      },
      isDisabledNext() {
        return (
          this.isClickedNextMonth || this.$store.state.eventsStatus.loadingRight
        );
      }
    },
    methods: {
      navigate(date) {
        this.$emit('navigate', date);
      },
      goPrevious() {
        this.navigate(this.prevMonthEvent);
        this.isClickedPrevMonth = true;
        setTimeout(() => {
          this.isClickedPrevMonth = false;
        }, 500);
      },
      goNext() {
        this.navigate(this.nextMonthEvent);
        this.isClickedNextMonth = true;
        setTimeout(() => {
          this.isClickedNextMonth = false;
        }, 500);
      }
    }
  };
</script>

<style>
  .calendar_header {
    position: relative;
  }

  .calendar_header button {
    text-decoration: none;
    height: 2em;
    font-size: 1em;
    line-height: 2em;
    margin: -1em 0 0 0;
    padding: 0 1rem;
    position: absolute;
    top: 54%;
    width: 2em;
    color: #16407d;
    background: none;
    transition: color .1s linear;
  }

  .calendar_header button[disabled] {
    color: #ddd;
  }

  .calendar_header button:not([disabled]):hover {
    color: #000;
  }

  .calendar_header .btn-prev {
    left: -5px;
  }

  .calendar_header .btn-next {
    right: 5px;
  }

  @media (--timeline-small) {
    .calendar_header h2 {
      font-size: 2.7rem;
    }
  }
</style>



