<template>
  <modal :on-close="close">
    <h3 slot="header" v-if="meeting && meeting.meetingCategory">{{meeting.meetingCategory.name}}</h3>
    <div class="event-content editor-text" v-if="meeting">
      <h1>{{meeting.agenda}}</h1>
      <div v-html="meeting.gist"></div>
      <p v-if="meeting.startDate">
        <span v-translate>Start date</span>: {{newDateFormat(meeting.startDate)}}
      </p>
      <p v-if="meeting.endDate">
        <span v-translate>End date</span>: {{newDateFormat(meeting.endDate)}}
      </p>

      <h3 class="organization-name" v-translate="">Participants</h3>
      <ul>
        <li v-for="participant in meeting.participants" :key="participant.id">{{participant.username}}</li>
      </ul>

      <add-to-calendar v-if="meeting && meeting.startDate && meeting.endDate"
        :title="meeting.agenda"
        :location="meeting.agenda"
        :details="meeting.gist"
        :start="new Date(meeting.startDate)"
        :end="new Date(meeting.endDate)"
        inline-template>
        <p class="calendar-links"><span v-translate>Add to Calendar</span>:
          &nbsp; <ical-calendar>iCalendar</ical-calendar>
          &nbsp;|&nbsp; <google-calendar>Google</google-calendar>
          &nbsp;|&nbsp; <microsoft-calendar>Outlook</microsoft-calendar>
        </p>
      </add-to-calendar>
    </div>
  </modal>
</template>
<script>
  import router from '../../router/index';
  import dateFormatter from '../../helpers/date-helper';
  import addToCalendar from '../../helpers/AddToCalendar';
  import Modal from '../Modal';

  export default {
    components: {
      Modal,
      addToCalendar
    },
    computed: {
      meeting() {
        return (
          this.$store.state.meetings[this.$store.state.route.params.id] || {}
        );
      }
    },
    methods: {
      close() {
        router.push({ name: 'date', params: this.$route.params });
      },
      newDateFormat(data) {
        return dateFormatter(data, 'DD.MM.YYYY HH:ss');
      }
    }
  };
</script>

<style>
  .calendar-links {
    color: #999;
    margin: 0;
    padding: 3rem 0 0;
  }

</style>



