<template>
  <timeline-activity :link="activityLink" :className="'grey-blue'">

    <img slot="image" class="organization-image"
         src="/static/img/1p.gif"
         style="backgroundImage:url('/static/img/icon-meeting.png')"
         :alt="data.meetingCategory.name"
         :title="data.meetingCategory.name"/>

    <h3 slot="title">{{data.meetingCategory.name|nameHyphenator}}</h3>

    <timeline-event-info
      :title="data.agenda"
      :startDate="data.startDate"
      :endDate="data.endDate"
    >
    </timeline-event-info>
  </timeline-activity>
</template>

<script>
  import dateFormatter from '../../helpers/date-helper';
  import TimelineEventInfo from './TimelineEventInfo';
  import TimelineActivity from './TimelineActivity';
  import nameHyphenator from '../../mixins/name-hyphenator-filter';

  export default {
    components: {
      TimelineEventInfo,
      TimelineActivity
    },
    mixins: [nameHyphenator],
    props: ['event'],
    computed: {
      data() {
        return this.$store.state.meetings[this.event.id] || {};
      },
      activityLink() {
        return {
          name: 'viewMeeting',
          params: {
            date: dateFormatter(this.data.startDate, 'YYYY-MM-DD'),
            id: this.data.id
          }
        };
      }
    }
  };
</script>



