import Vue from 'vue';
import VueTranslate from 'vue-translate-plugin';
import Raven from 'raven-js';
import RavenVue from 'raven-js/plugins/vue';
import { sync } from 'vuex-router-sync';
import GSignInButton from 'vue-google-signin-button';
import router from './router';
import store from './store';
import locales from './i18n';
import App from './components/App';
import iOSDetector from './helpers/ios-detector';
import VeeValidate from './helpers/vee-validator-rules';
import errorsDictionary from './i18n/vee-validate-locale';

require('intersection-observer');

if (process.env.NODE_ENV === 'production') {
  Raven.config('https://83830feff4de4e649b22d00eca4580d7@sentry.io/138427', {
    collectWindowErrors: true,
    fetchContext: true,
    linesOfContext: 40
  })
    .addPlugin(RavenVue, Vue)
    .install();
}

if (process.env.NODE_ENV === 'development') {
  Vue.config.performance = true;
}

document.body.className = iOSDetector() ? 'ios' : '';

Vue.use(VeeValidate, {
  dictionary: errorsDictionary(),
  locale: 'no'
});

Vue.use(VueTranslate);
Vue.use(GSignInButton);

sync(store, router);

const app = new Vue({
  router,
  store,
  locales,
  ...App
});

export { app, router, store };



