<template>
  <span class="calendar_day" v-bind:class="{ prevmonth: event.prevClass, nextmonth: event.nextClass, currentday: event.currentClass, activeday: event.activeClass, event: event.events }">
    <a href="#" @click.prevent="navigate(event.date)" v-if="event.events"><span>{{ event.day }}</span></a>
    <span v-else>{{ event.day }}</span>
  </span>
</template>


<script>
  import { createDate } from '../../helpers/week-number';
  import dateFormatter from '../../helpers/date-helper';

  export default {
    props: ['event'],
    methods: {
      navigate(date) {
        this.$emit('navigate', date);
      }
    }
  };
</script>

<style>
  .calendar_day {
    position: relative;
    border: 1px solid transparent;
  }

  .calendar_day > a,
  .calendar_day > span {
    text-decoration: none;
    width: 100%;
    text-align: center;
    display: inline-block;
    line-height: 1;
    box-sizing: border-box;
    color: #666;
    font-size: 11px;
    padding: .6rem .4rem;
  }
  .calendar_day > a {
     color: #16407d;
  }


  .calendar_day.prevmonth,
  .calendar_day.nextmonth {
    & > a, & > span {
      color:#ddd;
    }
  }
  .calendar_day.currentday { /* Today */
    & > a, & > span {
      color: blue;
    }
  }
  .calendar_day.activeday {
    border-color:#e73e42 !important;
  }

  .calendar_day.event span:after {
    background: #16407d;
    border-radius: 50%;
    bottom: .1rem;
    display: block;
    content: '';
    width: .5rem;
    height: .5rem;
    left: 50%;
    top:50%;
    margin: .5em 0 0 -.2rem;
    position: absolute;
  }

  .calendar_day.event:hover {
    border: 1px solid #16407d
  }
  .calendar_day.event.prevmonth:hover, .calendar_day.event.nextmonth:hover {
    border: 1px solid transparent
  }

  .calendar_day.event.nextmonth:after,
  .calendar_day.event.prevmonth:after{
    background:  #ddd;
  }
  .calendar_day.event.currentday:after {
    background: blue
  }

</style>



