<template>
  <div>
    <h2 v-translate>Enrollment</h2>
    <enrollment-link v-if="event.registrationLink" v-bind:enrollmentLink="event.registrationLink"></enrollment-link>
    <enrollment-panel v-if="!event.registrationLink" v-bind:event="event"></enrollment-panel>
  </div>
</template>

<style>
  .modal-container form.enroll-form {
    padding: 0;
    margin: 0;
  }

  .event .modal-container .form {
    padding: 0;
  }

  .event .modal-container .form .col-full input,
  .event .modal-container .form .col-1,
  .event .modal-container .form .col-2 {
    margin-bottom: 1.2rem;
  }

  .event .modal-container .form label.label-inline {
    position: relative;
    top: 9px;
  }
</style>

<script>
  import EnrollmentLink from '../Enrollment/EnrollmentLink';
  import EnrollmentPanel from '../Enrollment/EnrollmentPanel';

  export default {
    name: 'EventViewEnrollmentPage',
    props: ['event'],
    components: {
      EnrollmentLink,
      EnrollmentPanel
    }
  };
</script>



