class EventListenersHelperClass {
  eventListeners = [];

  constructor() {}

  addListener(el, event, callback) {
    this.eventListeners.push({ el, event, callback });
  }

  init() {
    this.eventListeners.forEach(listener => {
      listener.el.addEventListener(listener.event, listener.callback, false);
    });
  }

  destroy() {
    this.eventListeners.forEach(listener => {
      listener.el.removeEventListener(listener.event, listener.callback);
    });
    this.eventListeners = [];
  }
}

export default EventListenersHelperClass;



