<template>
  <div>
    <form v-if="login && isEnrollChecked" @submit.prevent="submitAction" id="enroll-form" name="enroll-form"
          class="enroll-form forms">
      <div v-if="isAlreadyEnrolled">
        <p><span v-translate>You have been already enroll on this event!</span></p>
      </div>
      <div v-else>
        <div v-if="formSubmitted">
          <h3 v-translate>Congratulation</h3>
          <p><span v-translate>Successfully</span> {{event.title}}</p>
        </div>
        <div v-else>
          <button type="submit" class="button-primary" v-bind:disabled="formSubmitted" v-translate>Enroll</button>
        </div>
      </div>
    </form>
    <div v-if="!login">
      <div v-if="hideRegister">
        <login-form></login-form>
        <button @click="hideRegister = !hideRegister" class="sign-in" v-translate>Register</button>
      </div>
      <div v-if="!hideRegister">
        <register-form></register-form>
        <button @click="hideRegister = !hideRegister" class="sign-in" v-translate>Login</button>
      </div>
    </div>
  </div>
</template>

<script>
  import { User } from '../../helpers/Db';
  import LoginForm from '../LoginForm';
  import RegisterForm from '../RegisterForm';

  export default {
    name: 'EnrollmentPanel',
    props: ['event'],
    components: {
      LoginForm,
      RegisterForm
    },
    data() {
      return {
        isEnrollChecked: false,
        isAlreadyEnrolled: true,
        mail: '',
        formSubmitted: false,
        hideRegister: true
      };
    },
    computed: {
      routeParams() {
        return this.$store.state.route.params;
      },
      login() {
        return Boolean(this.$store.state.user.username);
      }
    },
    mounted() {
      this.isEnrollChecked = false;
      this.checkUserEnroll();
    },
    watch: {
      '$store.state.user.username'() {
        this.checkUserEnroll();
      }
    },
    methods: {
      checkUserEnroll() {
        if (!this.$store.state.user.id) {
          return;
        }

        User.isUserEnroll({
          userId: this.$store.state.user.id,
          eventId: this.event.id
        }).then(data => {
          this.isEnrollChecked = true;
          this.isAlreadyEnrolled = Boolean(data.exists);
        });
      },
      submitAction() {
        this.$store
          .dispatch('saveSubscription', {
            eventId: this.event.id,
            userId: this.$store.state.user.id
          })
          .then(() => {
            this.formSubmitted = true;
          });
      }
    }
  };
</script>



