import SubscriptionClass from './subscription';
import EventClass from './event';
import MeetingClass from './meeting';
import LevelClass from './level';
import RegionClass from './region';
import CategoryClass from './category';
import OrganizationClass from './organization';
import UserClass from './user';

export const Organizations = new OrganizationClass();
export const Levels = new LevelClass();
export const Regions = new RegionClass();
export const Categories = new CategoryClass();
export const User = new UserClass();
export const Events = new EventClass();
export const Meetings = new MeetingClass();
export const Subscriptions = new SubscriptionClass();

export default {
  Organizations,
  Levels,
  Regions,
  Categories,
  Events,
  Meetings,
  User,
  Subscriptions
};



