<template>
  <footer class="calendar_footer">
    <a href="#" class="calendar_button__today" @click.prevent="navigate()">
      <span v-translate>Today</span>
    </a>
  </footer>
</template>

<script>
  import dateFormatter from '../../helpers/date-helper';

  export default {
    methods: {
      navigate() {
        this.$emit('navigate', this.$store.state.currentDate);
      }
    }
  };
</script>

<style>
  @import "variables.css";

  .calendar_footer {
    margin: .3rem .5rem;
    text-align: right;
  }

  .calendar_button__today {
    height: auto;
    display: inline;
    background: transparent;
    color: var(--blueColor);
    font-size: 12px;
    line-height: 1;
    border-bottom: 1px dashed var(--blueColor);
    text-decoration: none;
  }
</style>



