locale.fecha = {
  masks: {
    default: 'D MMM YY HH:mm:ss',
    short: 'DD/MM/YY HH:mm',
    shortDate: 'DD/MM/YY',
    mediumDate: 'D MMM YY',
    longDate: 'D MMMM YY',
    fullDate: 'dddd, D MMMM YY',
    shortTime: 'HH:mm',
    mediumTime: 'HH:mm:ss',
    longTime: 'HH:mm:ss.SSS'
  },
  DoFn(number) {
    const b = number % 10;
    const output =
      ~~(number % 100 / 10) === 1
        ? 'th'
        : b === 1 ? 'st' : b === 2 ? 'nd' : b === 3 ? 'rd' : 'th';
    return `${number}${output}`;
  },
  amPm: ['am', 'pm'],
  firstdayofweek: 0,
  dayNames: 'Sunday_Monday_Tuesday_Wednesday_Thursday_Friday_Saturday'.split(
    '_'
  ),
  dayNamesShort: 'Sun_Mon_Tue_Wed_Thu_Fri_Sat'.split('_'),
  monthNames: 'January_February_March_April_May_June_July_August_September_October_November_December'.split(
    '_'
  ),
  monthNamesShort: 'Jan_Feb_Mar_Apr_May_Jun_Jul_Aug_Sep_Oct_Nov_Dec'.split('_')
};

export default locale;



