<template>
  <modal :modal-class="'register'" v-bind:class="{profile: isLoggedIn}" :on-close="close">
    <h3 slot="header" v-translate>{{headerTitle}}</h3>
    <register-form></register-form>
    <router-link v-if="!isLoggedIn" :to="{ name: 'login', params: routeParams}" class="sign-in" v-translate>Login</router-link>
  </modal>
</template>

<script>
  import router from '../router/index';
  import Modal from './Modal';
  import RegisterForm from './RegisterForm';
  import notification from '../helpers/Notification';

  export default {
    props: ['edit-form'],
    components: {
      Modal,
      RegisterForm
    },
    computed: {
      routeParams() {
        return this.$store.state.route.params;
      },
      isLoggedIn() {
        return !!this.$store.state.user.username;
      },
      headerTitle() {
        return this.isLoggedIn ? 'Edit Profile' : 'Register';
      }
    },
    methods: {
      close: function() {
        router.push({ name: 'date', params: this.$route.params });
      }
    }
  };
</script>



