<template>
  <modal :modal-class="'login'" v-bind:class="{profile: isLoggedIn}" :on-close="close">
    <h3 slot="header" v-translate>{{headerTitle}}</h3>
    <login-form v-if="!isLoggedIn"></login-form>
    <user-profile v-if="isLoggedIn"></user-profile>
    <router-link v-if="!isLoggedIn" :to="{ name: 'register', params: routeParams}" class="sign-in" v-translate>Register</router-link>
  </modal>
</template>

<style>
  .login .form {
    padding-bottom: 0;
  }
  .sign-in {
    display: block;
    text-align: center;
    padding:2rem 1rem 2rem;
    color: #2d67bb;
    font-size: 1.3rem;
    background: none;
    text-transform: none;
    font-weight: normal;
  }
</style>

<script>
  import router from '../router/index';
  import Modal from './Modal';
  import LoginForm from './LoginForm';
  import UserProfile from './UserProfile';
  import notification from '../helpers/Notification';

  export default {
    components: {
      Modal,
      UserProfile,
      LoginForm
    },
    computed: {
      routeParams() {
        return this.$store.state.route.params;
      },
      isLoggedIn() {
        return !!this.$store.state.user.username;
      },
      headerTitle() {
        return this.isLoggedIn ? 'Profile' : 'Login';
      }
    },
    methods: {
      close: function() {
        router.push({ name: 'date', params: this.routeParams });
      }
    }
  };
</script>



