import spacetime from 'spacetime';
import curryRight from 'lodash/fp/curryRight';

export function createSpacetimeDate(dateInput) {
  'use strict';
  if (!dateInput) {
    return undefined;
  }

  return spacetime(dateInput).milliseconds(0);
}

export function createDate(dateInput) {
  'use strict';
  if (dateInput instanceof Date) {
    return dateInput;
  }

  if (typeof dateInput === 'number') {
    return new Date(dateInput);
  }

  if (
    typeof dateInput === 'string' &&
    dateInput.match(/^\d{4}-\d{2}-\d{2}$/g)
  ) {
    const dateArray = dateInput.split('-').map(Number);
    return new Date(
      createSpacetimeDate([
        dateArray[0],
        dateArray[1] - 1,
        dateArray[2],
        0,
        0,
        0,
        0
      ]).epoch
    );
  }

  return new Date(createSpacetimeDate(dateInput).epoch);
}

export const getStartOf = curryRight((incomeDate, period) => {
  'use strict';
  if (['day', 'week', 'month', 'year'].indexOf(period) < 0) {
    throw 'Unexpected period';
  }

  const date = createDate(
    createSpacetimeDate(incomeDate).startOf(period).epoch
  );
  date.setMilliseconds(0);
  return date;
});

export const getEndOf = curryRight((incomeDate, period) => {
  'use strict';
  if (['day', 'week', 'month', 'year'].indexOf(period) < 0) {
    throw 'Unexpected period';
  }
  const date = createDate(createSpacetimeDate(incomeDate).endOf(period).epoch);
  date.setMilliseconds(999);
  return date;
});

export const getStartOfDay = getStartOf('day');
export const getStartOfWeek = getStartOf('week');
export const getStartOfMonth = getStartOf('month');
export const getStartOfYear = getStartOf('year');

export const getEndOfDay = getEndOf('day');
export const getEndOfWeek = getEndOf('week');
export const getEndOfMonth = getEndOf('month');
export const getEndOfYear = getEndOf('year');

export function weekNumber(incomeDate) {
  const date = createSpacetimeDate(incomeDate);
  return date.week();
}

export default weekNumber;



