<template>

  <timeline-activity :link="activityLink" v-bind:id="'tl-event' + data.id" v-theme-by-category="data.categoryId">
    <organization-image slot="image" v-bind:organizationId="data.organizationId"></organization-image>
    <category-name slot="title" v-bind:categoryId="data.categoryId"></category-name>

    <timeline-event-info
      :title="data.title"
      :startDate="data.startDate"
      :endDate="data.endDate"
    >
    </timeline-event-info>
  </timeline-activity>
</template>

<script>
  import dateFormatter from '../../helpers/date-helper';
  import TimelineActivity from './TimelineActivity';
  import OrganizationImage from '../OrganizationImage';
  import CategoryName from '../CategoryName';
  import TimelineEventInfo from './TimelineEventInfo';
  import ThemeByCategory from '../../directives/ThemeByCategory';

  export default {
    components: {
      TimelineActivity,
      OrganizationImage,
      CategoryName,
      TimelineEventInfo
    },
    directives: {
      ThemeByCategory
    },
    data() {
      return {
        mouseTimeout: undefined,
        showOverlay: false
      };
    },
    props: ['event'],
    computed: {
      data() {
        return this.$store.state.events[this.event.id] || {};
      },
      activityLink() {
        return {
          name: 'viewEvent',
          params: {
            date: dateFormatter(this.data.startDate, 'YYYY-MM-DD'),
            id: this.data.id
          }
        };
      }
    },
    methods: {
      mouseOver() {
        if (this.mouseTimeout) {
          return;
        }

        this.mouseTimeout = setTimeout(() => {
          this.showOverlay = true;
        }, 200);
      },
      mouseLeave() {
        if (!this.mouseTimeout) {
          return;
        }
        this.showOverlay = false;
        clearTimeout(this.mouseTimeout);
        this.mouseTimeout = undefined;
      }
    }
  };
</script>



